<template>
	<Modal_body :close="false" title="Posielame e-mail klientovi" >

		<div v-show="status === 'sending'">
			<p>Prebieha odosielanie</p>
			<Bouncer_loading />
		</div>

		<div v-show="status === 'success'">
			<p>Na klientov e-mail bola zaslan zmluvn dokumentcia.</p>
			<p>Pre dokonenie, je potrebn, aby klient:</p>
			<ul>
				<li>otvoril zaslan e-mail,</li>
				<li>oboznmil sa so zmluvnou dokumentciou,</li>
				<li>klikol na odkaz, ktor ho presmeruje na tto strnku,</li>
				<li>potvrdil zadanm SMS kdu, ktor obdr na svoje telefnne slo, shlas s obsahom vetkch dokumentov.</li>
			</ul>
			<button
				@click="clear"
				class="btn btn-primary"
			>
				Vymaza a otvori nov kalkulciu
			</button>
		</div>

		<div v-show="status === 'fail'">
			<p>Odosielanie zlyhalo.</p>
			<p v-if="message" >
				{{ message }}
			</p>
		</div>

	</Modal_body>
</template>

<script>
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Signatures from '@/logic/requests/Signatures.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
		},
		components: {
			Bouncer_loading,
			Modal_body,
		},
		emits: [],
		data() {
			return {
				status: 'sending', // sending, fail, success
				message: '',
			}
		},
		created() {
			let checkboxes = STATE_modals().retrieve_modal_data();
			Signatures.offer_send(checkboxes).then(
				(response) => {
					if (response.status !== 200) {
						STATE_modals().close_them_all();
						return;
					}
					if (response.result.signature_required === 1) {
						this.status = 'success';
					} else {
						this.status = 'fail';
						this.message = JSON.stringify(response.result);
					}
				}
			);
		},
		methods: {
			clear()
			{
				Functions.clear_session();
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
