import STATE_core from '@/store/STATE_core.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Search_requests
*/
class Search_requests
{
	/**
	 * car_data_searcj
	*/
	car_data_search(type, value)
	{
		let requestParams = {
			action_name: 'car-data-search',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'api/car-data-search/?' + type + '=' + value, // spz=... || vin=...
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}

	/**
	 * ico_info
	*/
	ico_info(value)
	{
		let requestParams = {
			action_name: 'ico-info',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/ico-info/' + value + '/?' + 'type=' + STATE_core().product_group_id(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * car_valuation_options
	*/
	car_valuation_options(param_string)
	{
		let requestParams = {
			action_name: 'car-valuation-options',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'api/car-valuation-options/?' + param_string,
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * car_valuation_price
	*/
	car_valuation_price(param_string)
	{
		let requestParams = {
			action_name: 'car-valuation-price',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'api/car-valuation-price/?' + param_string,
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Search_requests;
