<template>
	<Modal_body :close="false" title="Chbajca licencia" >

		<div class="text-center">
			<p>
				Pre prcu s tmto produktom nemte poadovan licenciu.
			</p>

			<form
				v-show="!loading"
				@submit="submit_clearance"
			>
				<input
					v-model="value"
					ref="input"
					name="agent_clearance"
					type="text"
					placeholder="Doplte kd agenta"
					class="form-control mb-2"
				>
				<button
					type="submit"
					class="btn btn-primary mb-3"
				>
					Odosla
				</button>
			</form>

			<div
				v-show="loading"
				class="mb-3"
			>
				<h4>
					Overujeme kd
				</h4>
				<Bouncer_loading />
			</div>

			<div
				v-show="failed"
				class="mb-3"
			>
				<h4>
					Overovanie kdu zlyhalo.
				</h4>
			</div>

			<p>
				Ak dolo k nedorozumeniu, prosm kontaktuje ns <br>na e-mailovej adrese:
				<a :href="mailto">
					{{ email }}
				</a>
			</p>
		</div>

	</Modal_body>
</template>

<script>
	import Constants from '@/logic/Constants.js';
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Group_user_form from '@/logic/requests/Group_user_form.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_core from '@/store/STATE_core.js';
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
		},
		components: {
			Modal_body,
			Bouncer_loading,
		},
		emits: [],
		data() {
			return {
				loading: false,
				failed: false,
				value: '',
			}
		},
		created() {
		},
		mounted() {
			this.$nextTick(
				() => {
					this.$refs.input.focus();
				}
			);
		},
		methods: {
			submit_clearance(event)
			{
				event.preventDefault();
				this.loading = true;
				STATE_params().save_agent_code(this.value);
				Group_user_form.get_group_form().then(
					(response) => {
						this.loading = false;
						if (response.status !== 200) {
							this.failed = true;
							return;
						}
						// State.store_the_form(response.result.form);
						// STATE_params().save_agent_code(this.value);
						// i dont use form from this request, just try to reinit step1, strange workaround, should change
						STATE_modals().close_them_all();
						STATE_core().change_step(-1);
						setTimeout(
							() => { STATE_core().change_step(1); },
							10
						);
					}
				);
			}
		},
		computed: {
			email()
			{
				return Constants.email_address();
			},
			mailto()
			{
				return 'mailto:' + this.email;
			},
		},
		watch: {
		},
	}
</script>
