<template>
	<div
		:id="product_id"
		class="row product g-0"
		data-cy="product"
	>
		<!-- AREA product info -->
		<div class="col-lg-10 pr_area_info position-relative">

			<!-- promo -->
			<div
				v-show="p_data.product.promo === '1' && p_data.product.promo === '2'"
				class="pr_ribbon"
			>
				<span>N TIP</span>
			</div>

			<!-- top part -->
			<div class="pr_top">
				<div class="row g-0">

					<!-- BOX logo -->
					<div class="col-lg-2">
						<div class="pr_box_logo text-center">
							<img :src="p_data.logo">
						</div>
					</div>

					<div class="col-lg-4">
						<!-- BOX title, rating etc. box -->
						<div class="pr_box_name text-center text-lg-start py-1 py-lg-0">

							<!-- title, id only for info -->
							<h4 :id="'#' + product_id" >
								{{ product_name }}
							</h4>

							<!-- stars -->
							<div
								v-show="p_data.product.stars > 0"
								class="pr_rating"
							>
								<span
									v-for="index in 5"
									:class="[index > p_data.product.stars ? 'i_star_empty' : 'i_star']"
								></span>
							</div>

							<div class="pr_select">
								<!-- spoluucast / krytie - dropdown -->
								<Select
									v-for="(field_data, field_id) in p_data.spoluucast_krytie"
									:field_id="field_id"
									:field_data="field_data"
									emit_name="recalculate_product"
									@recalculate_product="recalculate_product"
								/>
								<!-- spoluucast plaintext -->
								<p
									v-show="!p_data.spoluucast_krytie"
								>
									{{ p_data.product.spoluucast_text }}
								</p>
							</div>

							<!-- detail modal trigger-->
							<div class="detail-compare text-nowrap">
								<span class="pr_detail">
									<button
										v-show="p_data.product.detail === '2'"
										@click="open_product_detail"
										class="btn-text i_anchor"
									>
										<span class="i_detail"></span>
										info o produkte
									</button>
									<!-- graph trigger -->
									<!-- <a href="#" class="detail-modal hidden" data-toggle="modal" data-target="detail" product-graph_button="">
										<span class="i_detail_modal"></span>GRAF
									</a> -->
								</span>
							</div>

						</div>
					</div>
					<hr class="pr_divider d-block d-lg-none">
					<!-- BOX quick overview -->
					<div class="col-lg-6">
						<div class="pr_box_overview">
							<ul>

								<!-- icons -->
								<li
									v-for="icon in p_data.icons"
									:class="icon.class"
								>
									{{ icon.title }}
									<div
										v-if="icon.tooltip"
										class="ttip ttip-top"
									>
									()
										<span class="ttip_text">
											{{ icon.tooltip }}
										</span>
									</div>
								</li>

							</ul>
						</div>
					</div>
				</div>
			</div>
			<hr class="pr_divider">

			<!-- bottom part -->
			<div class="pr_bottom">
				<div class="row g-0">
					<div class="col-lg-6">

						<!-- BOX benefits -->
						<div class="pr_box_included">
							<ul>
								<li
									v-for="benefit in p_data.benefits"
									:class="benefit.class"
								>
									{{ benefit.title }}
								</li>
							</ul>
						</div>

					</div>
					<hr class="pr_divider d-block d-lg-none">
					<div class="col-lg-6">
						<!-- BOX options / product form -->
						<div class="pr_box_options">
							<ul>

								<!-- product inputs -->
								<li
									v-for="(field_data, field_id) in p_data.inputs"
								>
									<Checkbox
										v-if="field_data.type === 'checkbox'"
										:field_id="field_id"
										:field_data="field_data"
										emit_name="recalculate_product"
										@recalculate_product="recalculate_product"
									/>
									<Select
										v-if="field_data.type === 'select'"
										:field_id="field_id"
										:field_data="field_data"
										emit_name="recalculate_product"
										@recalculate_product="recalculate_product"
									/>
									<Date
										v-if="field_data.type === 'date'"
										:field_id="field_id"
										:field_data="field_data"
										emit_name="recalculate_product"
										@recalculate_product="recalculate_product"
									/>
								</li>

							</ul>
						</div>
					</div>
				</div>

				<!-- crossell -->
				<div
					v-if="p_data.product.crossell"
					class="pr_bundle"
				>
					<span class="bundle_img"></span>
					<p>
						{{ p_data.product.crossell }}
					</p>
					<!-- cta not in use -->
					<!-- <span class="bundle_cta"><a href="#">vypota cenu</a></span> -->
				</div>

			</div>
		</div>

		<!-- AREA product price & cta -->
		<div class="col-lg-2">
			<div class="pr_area_price d-flex flex-column flex-sm-row flex-lg-column text-center h-100">

				<!-- discount -->
				<div class="pr_discount">
					<span
						v-if="p_data.price.base > 0"
						title="Zkladn cena pred zavami"
						class="before_discount hidden"
					>
						{{ p_data.price.base }}
					</span>
					<span
						v-if="p_data.price.discount > 0"
						title="Celkov zava"
						class="discount_tag hidden"
					>
						{{ p_data.price.base }}
					</span>

				</div>

				<!-- price -->
				<div v-show="!loading" class="pr_price">
					<span class="amount">{{ p_data.price.for_period }}</span>
					<span class="text-default">{{ p_data.price.currency }}</span>
					<span class="period">{{ p_data.price.period }}</span>
				</div>

				<!-- loading -->
				<div v-show="loading" class="pr_price">
					<Bouncer_loading />
				</div>

				<!-- product select button -->
				<div
					v-show="!loading"
					class="pr_cta"
				>

					<!-- error -->
					<span
						v-if="p_data.error.info"
						class="btn btn-inactive w-100"
					>
						<span
							:class="p_data.error.alt_text ? 'viacinfo' : ''"
						>
							{{ p_data.error.alt_text ? 'VIAC INFO' : 'Ned sa poisti' }}
						</span>
						<div
							v-show="p_data.error.info"
							class="ttip"
						>
							?
							<span
								v-html="p_data.error.info"
								class="ttip_text"
							></span>
						</div>
					</span>

					<!-- product select -->
					<button
						v-else-if="p_data.button.show === '1'"
						@click="select_this_product()"
						class="btn btn-xlg btn-right btn-accent w-100"
					>
						Vybra
					</button>

					<!-- warning -->
					<div
						v-if="p_data.button.show === '1' && p_data.product.warning"
						class="ttip pr_warning"
					>
						!
						<span class="ttip_text">
							{{ p_data.product.warning }}
						</span>
					</div>

				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Product_requests from '@/logic/requests/Product_requests.js';
	import Product_helpers from '@/components/form_app/product/Product_helpers.js';
	import Checkbox from '@/components/forms/basic_inputs/Checkbox.vue';
	import Select from '@/components/forms/basic_inputs/Select.vue';
	import Date from '@/components/forms/advanced_inputs/Date.vue';
	import Functions from '@/logic/Functions.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
			product_id: String,
			product_name: String,
			response: Object,
		},
		components: {
			Checkbox,
			Select,
			Date,
			Bouncer_loading,
		},
		emits: ['recalculate_product'],
		data() {
			return {
				p_data: {
					logo: '',
					error: {
						info: '',
						alt_text: false,
					},
					button: {
						show: '0',
						next_form: '',
						next_contract: '',
					},
					price: {
						for_period: '',
						currency: '',
						period: '',
						base: '',
						discount: '',
					},
					product: {
						promo: '',
						stars: 0,
						spoluucast_text: '',
						detail: '',
						detail_template: '',
						crossell: '',
						warning: '',
					},
					icons: [
						// {
						// 	title: '',
						// 	tooltip: '',
						// 	order: 0,
						// 	class: '',
						// },
						// ...
					],
					benefits: [
						{
							title: '',
							class: '',
						},
					],
					inputs: {
						// kooperativa_krytie: {
						// 	type: 'select',
						// 	enum: {...},
						// 	type_settings: {...},
						// },
						// ...
					},
					spoluucast_krytie: {
						// havarijne_kooperativa_spoluucast: {
						// 	type: 'select',
						// 	enum: {...},
						// 	type_settings: {...},
						// },
					},
				},
				loading: true,
			}
		},
		created() {
			this.asign_values();
		},
		watch: {
			response()
			{
				this.asign_values();
			},
		},
		methods: {
			asign_values()
			{
				if (typeof this.response !== 'object') {
					// reset this.p_data to original state when recalculate happens
					Object.assign(this.$data, this.$options.data());
					return;
				}
				// result inside product result
				let result = this.response.result[this.product_id].result;
				// frekvenciaNP
				let period = Product_helpers.find_period(this.response);
				// product data
				this.p_data = {
					logo: result?.head?.logo?.comment,
					period: period,
					error: {
						info: Product_helpers.error_tooltip(result),
						alt_text: Product_helpers.check_alt_text(),
					},
					price: {
						for_period: Product_helpers.price_for_period(result, period),
						currency: result?.result?.currency?.result || '',
						period: Product_helpers.label_for_period(result, period),
						base: result?.result?.base?.result,
						discount: result?.result?.discount?.result,
					},
					button: {
						show: result?.next?.next?.result,
						next_form: result?.next?.next_form?.result,
						next_contract: result?.next?.next_contract_html?.result,
					},
					product: {
						promo: result?.product?.promo?.result,
						stars: parseInt(result?.product?.stars?.result, 10),
						spoluucast_text: result?.product?.spoluucast?.formula_name,
						detail: result?.product?.detail?.result,
						detail_template: result?.product?.detail?.comment,
						crossell: result?.product?.crossell.result === '1' ? result?.product?.crossell?.comment : '',
						warning: result?.result?.warning?.result,
					},
					icons: Product_helpers.sort_icons(result?.icon),
					benefits: Product_helpers.sort_benefits(result?.benefit),
					inputs: Product_helpers.product_form_formatter(result?.results_form?.comment, true, false),
					spoluucast_krytie: Product_helpers.product_form_formatter(result?.results_form?.comment, true, true),
				};
				this.loading = false;
			},
			select_this_product() {
				this.loading = true;
				// store selected product for later usage
				STATE_params().set_selected_product(
					this.product_id,
					this.p_data.button.next_form,
					this.p_data.button.next_contract,
					this.p_data.period
				);
				// select request and redirect
				Product_requests.select_product().then(
					(response) => {
						if (response.result.response !== 'success') {
							return;
						}
						STATE_core().change_step(3);
					}
				);
			},
			open_product_detail()
			{
				// getting documents actually requires selected product id
				STATE_params().set_selected_product(
					this.product_id,
					null,
					null,
					null
				);
				// modal needs some info for gethtml request
				STATE_modals().store_modal_data(
					{
						product_name: this.product_name,
						detail_template: this.p_data.product.detail_template
					}
				);
				// open modal, request starts on created()
				STATE_modals().open_it('product_detail');
			},
			recalculate_product()
			{
				// take form data and launch calculate from step2
				let input_keys = Object.keys(this.p_data.inputs).concat(Object.keys(this.p_data.spoluucast_krytie));
				let form_data = Functions.serialize_specific_inputs(input_keys);
				this.$emit('recalculate_product', this.product_id, form_data);
			},
		},
		computed: {
		},
	}
</script>
