import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';
import STATE_params from '@/store/STATE_params.js';

/**
 * Get_form_html
*/
class Get_form_html
{
	/**
	 * get_it
	*/
	get_it(parameter)
	{
		if (parameter === false) {
			parameter = STATE_params().get_selected_product().next_contract;
		}

		let requestParams = {
			action_name: 'gethtml',
			method: 'POST',
			url: STATE_core().endpoint_url() + 'online/gethtml/' + '?id=' + parameter + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Get_form_html;
