import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';
import STATE_params from '@/store/STATE_params.js';

/**
 * Calculate_requests
*/
class Calculate_requests
{
	/**
	 * get_calculate
	*/
	get_calculate(/*int*/product_id, /*null|str*/form_data, /*int*/period)
	{
		let requestParams = {
			action_name: 'calculate_step2',
			method: 'POST',
			url: STATE_core().endpoint_url() + 'online/calculate/' + '?id=' + product_id + '&json=1&period=' + period + Functions.add_token_as_parameter(),
			body: form_data,
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {
				if (response.status === 408) {
					// when calculate in step2 can not deliver product, we repeat results requests which should deliver when calculated
					// it delivers all calculated products, but class takes only one with its ID
					console.log('#' + product_id + ' has 408, going for results request');
					return this.repeat_results();
				}
				// normal, didnt timeout
				return response;
			}
		).catch( (error) => { throw error; } );
	}


	/**
	 * repeat_results
	*/
	repeat_results()
	{
		let requestParams = {
			action_name: 'results',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/results/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then().catch( (error) => { throw error; } );
	}


	/**
	 * check_calculate
	*/
	check_calculate()
	{
		let requestParams = {
			action_name: 'calculate_step4',
			method: 'POST',
			url: STATE_core().endpoint_url() + 'online/calculate/' + '?' + Functions.add_token_as_parameter(),
			body: null,
		};
		requestParams.url += '&id=' + STATE_params().get_selected_product().product_id + '&period=' + STATE_params().get_selected_product().period;

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then().catch( (error) => { throw error; } );
	}
}

export default new Calculate_requests;
