<template>
	<div class="calc_title_area" data-cy="title">
		<h2>
			Nali sme
			<span
				:title="'z ' + total + ' prepotavanch'"
				class="text-accent"
			>
				{{ products_found }}
			</span>
		</h2>
		<div class="results_deliver">
			<button
				@click="open_email_modal"
				class="btn-text i_anchor"
			>
				<span class="i_email"></span>
				posla e-mailom
			</button>
			<button
				@click="download_pdf"
				class="btn-text i_anchor"
			>
				<Bouncer_loading
					v-if="downloading_pdf"
					size="small"
				/>
				<span
					v-else
					class="i_download"
				></span>
				stiahnu pdf
			</button>
		</div>
	</div>
</template>

<script>
	import Get_pdf from '@/logic/requests/Get_pdf.js';
	import Functions from '@/logic/Functions.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import STATE_modals from '@/store/STATE_modals.js';

	export default {
		props: {
			loaded: Number,
			total: Number,
		},
		components: {
			Bouncer_loading,
		},
		data() {
			return {
				downloading_pdf: false,
			}
		},
		created() {
		},
		methods: {
			open_email_modal() {
				STATE_modals().open_it('email');
			},
			download_pdf() {
				this.downloading_pdf = true;
				Get_pdf.download_global_pdf().then(
					(response) => {
						this.downloading_pdf = false;
						Functions.downloadBase64('Ponuka.pdf', response.result.pdf);
					}
				);
			},
		},
		computed: {
			products_found() {
				if (this.loaded === 1) {
					return this.loaded + ' produkt';
				} else if (this.loaded > 1 && this.loaded < 5) {
					return this.loaded + ' produkty';
				} else /* 0 or 5+ */ {
					return this.loaded + ' produktov';
				}
			},
		},
	}
</script>
