import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Signatures
*/
class Signatures
{
	/**
	 * offer_send
	*/
	offer_send(checkboxes)
	{
		let requestParams = {
			action_name: 'signature-offer-send',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/signature-offer-send/?' + Functions.serialize_object(checkboxes) + Functions.add_token_as_parameter() + '&signature_state=client',
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * code_send
	*/
	code_send()
	{
		let requestParams = {
			action_name: 'signature-code-send',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/signature-code-send/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * code_check
	*/
	code_check(checkboxes, code)
	{
		let requestParams = {
			action_name: 'signature-code-check',
			method: 'POST',
			url: STATE_core().endpoint_url() + 'online/signature-code-check/?' + Functions.serialize_object(checkboxes) + Functions.add_token_as_parameter(),
			body: JSON.stringify({ 'code': code }),
			content_type: 'application/json',
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * update_signature_state
	*/
	update_signature_state()
	{
		// probably should use validate api??
		let requestParams = {
			action_name: 'results',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/results/?signature_state=agent_autoconfirm' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Signatures;
