import STATE_params from '@/store/STATE_params.js';
import Functions from '@/logic/Functions.js';
import Search_requests from '@/logic/requests/Search_requests.js';

let mixin = {
	props: {
	},
	components: {
	},
	emits: [],
	data() {
		return {
			watched_id: 'havarijne_auto_vybava', // reqired, listener
			cena_id: 'havarijne_auto_cena', // required, changing
			vyroba_id: 'havarijne_rokvyroby', // required
			cena_date: 'havarijne_auto_registracia', // optional, formated date
			cena_required: ['havarijne_auto_znacka', 'havarijne_auto_model', 'havarijne_objem', 'havarijne_vykon', 'havarijne_palivo'],
			cena_optional: ['havarijne_hmotnost', 'havarijne_rokvyroby', 'havarijne_auto_prevodovka', 'havarijne_auto_registracia', 'havarijne_auto_pocet_sedadiel', 'havarijne_auto_karoseria'],
			stanovenie_id: 'havarijne_auto_cena_stanovenie', // disable/enable
		}
	},
	created() {
		this.cena_request();
	},
	methods: {
		cena_request()
		{
			if (this.cached_listener === null) {
				return;
			}
			let vybava_value = structuredClone(this.cached_listener);
			let vyroba_value = STATE_params().get_form_value(this.vyroba_id);
			if (vybava_value === '' || vyroba_value === '') {
				this.cena_disable = false;
				return;
			}
			this.cena_disable = true;
			let params = {};
			let i, len = this.cena_required.length;
			for (i = 0; i < len; i++) {
				params[this.cena_required[i]] = STATE_params().get_form_value(this.cena_required[i]);
			}
			len = this.cena_optional.length;
			for (i = 0; i < len; i++) {
				let value = STATE_params().get_form_value(this.cena_optional[i]);
				if (value !== '' && value !== undefined) {
					if (this.cena_optional[i] === this.cena_date) {
						value = Functions.format_car_request_date(value);
					}
					params[this.cena_optional[i]] = value;
				}
			}
			params[this.watched_id] = vybava_value;
			params = Functions.serialize_object(params);
			Search_requests.car_valuation_price(params).then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					// STATE_params().set_form_value(this.cena_id, response.result.data?.[0]?.[this.cena_id]);
					this.value = response.result.data?.[0]?.[this.cena_id];
				}
			);
		},
	},
	computed: {
		cached_listener()
		{
			if (this.field_id !== this.cena_id) {
				return null;
			}
			return STATE_params().get_form_value(this.watched_id);
		},
		cached_stanovenie()
		{
			if (this.field_id !== this.cena_id) {
				return null;
			}
			return STATE_params().get_form_value(this.stanovenie_id);
		},
	},
	watch: {
		cached_listener()
		{
			this.cena_request();
		},
		cached_stanovenie()
		{
			// 1 invoice, 2 eurotax
			if (this.cached_stanovenie === '2') {
				this.cena_disable = false;
				STATE_params().delete_form_value(this.watched_id);
			}
		},
	},
};

export default mixin;
