import STATE_params from '@/store/STATE_params.js';

let mixin = {
	props: {
	},
	components: {
	},
	emits: [],
	data() {
		return {
			dynamic: {
				// parent_id: child_id,
				majetok_podlazie: 'majetok_poschodie',
			},
		}
	},
	created() {
	},
	methods: {
	},
	computed: {
		dynamic_parent()
		{
			if (!this.dynamic.hasOwnProperty(this.field_id)) {
				return null;
			}
			return STATE_params().get_form_value(this.dynamic[this.field_id]);
		},
	},
	watch: {
		dynamic_parent()
		{
			if (isNaN(this.dynamic_parent) || this.dynamic_parent === '') {
				this.value = '';
			} else {
				this.value = parseInt(this.dynamic_parent, 10) + 1;
			}
		},
	},
};

export default mixin;
