<template>
	<div class="form-check">
		<input
			:id="field_id"
			:name="field_id"
			v-model="value"
			true-value="1"
			false-value="0"
			:disabled="field_data.disabled"
			type="checkbox"
			class="form-check-input"
		>
		<label
			v-show="emit_name !== 'validate_global_changed'"
			:for="field_id"
			class="form-check-label"
		>
			{{ field_data.label }}
		</label>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
					if (this.emit_name !== undefined) {
						this.$emit(this.emit_name);
					}
				},
			},
		},
		watch: {
		},
	}
</script>
