<template>
	<div class="docs_landing_scope">
		<div v-for="(occurence) in clear_template_data" >
			<div
				v-if="occurence.key === 'html_key'"
				v-html="occurence.content"
				class="dl_item_text"
			></div>
			<Send_button
				v-else-if="occurence.key === 'button_key'"
				:input="occurence.content"
			/>
			<Upload_input
				v-else-if="occurence.key === 'input_key'"
				:tag="occurence.tag"
				@refresh_uploaded_ids="refresh_uploaded_ids"
			/>
			<div
				v-else-if="occurence.key === 'tiles_key'"
				class="row"
			>
				<div
					v-for="(tag) in occurence.all_tags"
					class="col-12 col-md-4"
				>
					<Upload_input
						:tag="tag"
						@refresh_uploaded_ids="refresh_uploaded_ids"
					/>
				</div>
			</div>
			<div
				v-else-if="occurence.key === 'grouped_key'"
			>
				<Upload_input
					v-for="(tag) in occurence.all_tags"
					:tag="tag"
					@refresh_uploaded_ids="refresh_uploaded_ids"
				/>
			</div>
		</div>
		<div v-if="debug" >
			<hr>
			<pre>
				{{ session_debug }}
			</pre>
		</div>
		<Testing_template />
	</div>
</template>

<script>
	import Upload_input from '@/components/docs_landing/Upload_input.vue';
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Get_session_docs from '@/logic/requests/Get_session_docs.js';
	import STATE_core from '@/store/STATE_core.js';
	import Testing_template from '@/components/docs_landing/Testing_template.vue';
	import Send_button from '@/components/docs_landing/Send_button.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_docs_landing from '@/store/STATE_docs_landing.js';
	import Template_parser from '@/components/docs_landing/Template_parser.js';

	export default {
		props: {
		},
		components: {
			Upload_input,
			Testing_template,
			Send_button,
		},
		data() {
			return {
				debug: false,
				template_name: '',
				template_raw: '',
				template_array: [],
				session_debug: '',
				debug_template: `
					<h2>Document upload landing page</h2>
					%% type=grouped tags=udalost group=udalost heading="kodov udalos zznamy" max=4 %%
					%% send_button send=upload_kasko_protokol_foto group=udalost plus_button=1 text="Deus vult dolor sit amet..." %%
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					%% type=single tags=tp send=upload_kasko_tp_foto heading="Kapota auta" multiple=3 %%
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
					</p>
					%% type=single tags=faktura send=upload_kasko_faktura_foto heading="Technick preukaz" %%
					<p>
						Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
					</p>
					<p>
						Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.
					</p>
					%%
						type=tiles group=kufre
						%
						tags=protokol1 heading="Kufor 1" image="07_lavobok" required=1
						%
						tags=protokol2 heading="Kufor 2" image="08_pravobok" required=1
						%
						tags=protokol3 heading="Kufor 3" image="09_celne_sklo" required=1
						%
						tags=protokol4 heading="Kufor 4" required=0
						%
						tags=protokol5 heading="Kufor 5" required=0
					%%
					%% send_button send=upload_kasko_protokol_foto group=kufre text="Deus vult dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam" %%
				`,
			}
		},
		created() {
			if (this.we_dont_have_token()) {
				return;
			}
			Promise.resolve().then(
				() => {
					return this.get_template_token_name();
				}
			).then(
				() => {
					return Promise.all(
						[
							this.get_document_template(),
							this.get_session_documents(),
						]
					);
				}
			).then(
				() => {
					this.template_array = Template_parser.process_template(this.template_raw);
				}
			);
		},
		methods: {
			we_dont_have_token()
			{
				if (STATE_core().get_session_token() === '') {
					console.log('Session token is required.');
					return true;
				}
				return false;
			},
			get_template_token_name()
			{
				if (this.debug) {
					console.log('using template: DEBUG');
					return Promise.resolve();
				}
				return Session_data.get_data().then(
					(response) => {
						this.template_name = response.result.call_params?.osm_doc_template || 'upload_placeholder';
						console.log('using template: ' + this.template_name);
					}
				);
			},
			get_document_template()
			{
				if (this.debug) {
					this.template_raw = this.debug_template;
					return Promise.resolve();
				}
				return Get_form_html.get_it(this.template_name).then(
					(response) => {
						this.template_raw = response.result.error || response.result.html;
					}
				);
			},
			get_session_documents()
			{
				return Get_session_docs.session_documents(true).then(
					(response) => {
						this.session_debug = JSON.stringify(response, null, 2);
						STATE_docs_landing().set_doc_session_state(response.result.data?.documents);
					}
				);
			},
			refresh_uploaded_ids()
			{
				this.get_session_documents();
			},
		},
		computed: {
			clear_template_data()
			{
				return this.template_array;
			},
		},
	}
</script>
