import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Confirm_requests
*/
class Confirm_requests
{
	/**
	 * confirm_session
	*/
	confirm_session(checkboxes)
	{
		let requestParams = {
			action_name: 'confirm',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/confirm/?' + Functions.serialize_object(checkboxes) + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {
				if (response.status === 408) {
					// when confirm in step4 request timeouts, we repeat getsessioninfo
					return this.repeat_getsessioninfo();
				}
				// normal, didnt timeout
				return response;
			}
		).catch( (error) => { throw error; } );
	}


	/**
	 * repeat_confirm
	*/
	repeat_confirm()
	{
		let requestParams = {
			action_name: 'confirm',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/confirm/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * repeat_getsessioninfo
	*/
	async repeat_getsessioninfo()
	{
		console.log('wait 25 seconds then try getsessioninfo');
		let wait = await new Promise(
			(resolve, reject) => {
				setTimeout(
					() => { resolve(); },
					25000
				);
			}
		);

		let requestParams = {
			action_name: 'getsessioninfo',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getsessioninfo/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then().catch( (error) => { throw error; } );
	}
}

export default new Confirm_requests;
