import { defineStore } from 'pinia';

export default defineStore(
	'STATE_modals',
	{
		state: () => {
			return {
				opened: {
					// preloader: false, // not in use, remove after while, 01.03.2023
					loading: false,
					clearance: false,
					email: false,
					product_detail: false,
					where_to_session: false,
					calculate_error_in_validate: false,
					calculate_error_in_form: false,
					marketing: false,
					marketing_ek: false,
					confirm_simple_error: false,
					signature_agent: false,
					signature_client: false,
				},
				temporary_data: {},
			};
		},
		getters: {
			// opened
			which_is_open()
			{
				return () => {
					// returns name of currently open modal, or null if none is open
					for (let modal_name in this.opened) { if (!this.opened.hasOwnProperty(modal_name)) { continue; }
						if (this.opened[modal_name] === true) {
							return modal_name;
						}
					}
					return null;
				}
			},
			// data
			retrieve_modal_data()
			{
				return () => {
					return this.temporary_data;
				}
			},
		},
		actions: {
			// close
			close_them_all()
			{
				for (let modal_name in this.opened) { if (!this.opened.hasOwnProperty(modal_name)) { continue; }
					this.opened[modal_name] = false;
				}
			},
			// open
			open_it(modal_name)
			{
				if (!this.opened.hasOwnProperty(modal_name)) {
					console.log('this modal does not exist');
					return;
				}
				this.close_them_all();
				this.opened[modal_name] = true;
			},
			// data
			store_modal_data(modal_data)
			{
				this.temporary_data = modal_data;
			},
		},
	}
);
