import { defineStore } from 'pinia';

export default defineStore(
	'STATE_error',
	{
		state: () => {
			return {
				headers: {},
				params: {},
				date: '',
				session: '',
				step: 0,
				status: 0,
				result: {},
			};
		},
		getters: {
			get_last_request()
			{
				return () => {
					return {
						url: this.params?.url,
						date: this.date,
						status: this.status,
						session: this.session,
						step: this.step,
					};
				}
			},
		},
		actions: {
			set_last_request(headers, params, date, session, step, response)
			{
				this.headers = headers;
				this.params = params;
				this.date = date;
				this.session = session;
				this.step = step;
				this.status = response?.status;
				this.result = response?.result;
			},
		},
	}
);
