import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Get_session_docs
*/
class Get_session_docs
{
	/**
	 * session_documents
	*/
	session_documents(only_user_uploaded)
	{
		let requestParams = {
			action_name: 'getsessiondocuments',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getsessiondocuments/?' + Functions.add_token_as_parameter(), // &update=1 forces document update
		};
		if (only_user_uploaded) {
			requestParams.url += '&user_uploaded=true';
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_session_document
	*/
	get_session_document(document_id)
	{
		let requestParams = {
			action_name: 'getsessiondocument',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getsessiondocument/' + document_id + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_session_document_preview
	*/
	get_session_document_preview(document_id, size)
	{
		let requestParams = {
			action_name: 'getsessiondocument',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getsessiondocument/' + document_id + '/?' + Functions.add_token_as_parameter(),
		};
		requestParams.url += '&filters=thumbnail&thumbnail_mode=outbound&thumbnail_width=' + size + '&thumbnail_height=' + size;

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * upload_session_document
	*/
	upload_session_document(payload)
	{
		let requestParams = {
			action_name: 'createsessiondocument',
			method: 'POST',
			body: payload,
			content_type: null, // 'multipart/form-data' filled by browser
			url: STATE_core().endpoint_url() + 'online/createsessiondocument/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * delete_session_document
	*/
	delete_session_document(id)
	{
		let requestParams = {
			action_name: 'delete-uploaded-session-documents',
			method: 'DELETE',
			url: STATE_core().endpoint_url() + 'online/delete-uploaded-session-documents/?id=' + id + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Get_session_docs;
