import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Data_record
*/
class Data_record
{
	/**
	 * create_data_record
	*/
	create_data_record(record_token)
	{
		let requestParams = {
			action_name: 'create-data-record-request',
			method: 'POST',
			url: STATE_core().endpoint_url() + 'online/create-data-record-request/?data_record_token=' + record_token + Functions.add_token_as_parameter(),
			body: JSON.stringify( {data_record_data: ['test']} ),
			content_type: 'application/json',
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Data_record;
