import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';
import STATE_params from '@/store/STATE_params.js';

/**
 * Group_user_form
*/
class Group_user_form
{
	/**
	 * getform
	*/
	getform()
	{
		let step = STATE_core().current_step();
		if (step === 1) {
			return this.get_group_form();
		} else if (step === 3) {
			return this.get_user_form();
		}
	}


	/**
	 * validateform
	*/
	validateform()
	{
		let step = STATE_core().current_step();
		if (step === 1) {
			return this.validate_group_form();
		} else if (step === 3) {
			return this.validate_user_form();
		}
	}


	/**
	 * get_group_form
	*/
	get_group_form()
	{
		let requestParams = {
			action_name: 'getgroupform',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getgroupform/' + STATE_core().product_group_id() + '/?global=2&json=1',
		};
		requestParams.url += Functions.add_token_as_parameter();
		requestParams.url += STATE_params().agent_code_param();
		requestParams.url += STATE_params().add_filtered_param();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}

	/**
	 * validate_group_form
	*/
	validate_group_form()
	{
		let requestParams = {
			action_name: 'validategroupform',
			method: 'POST',
			body: Functions.serialize_user_input(),
			url: STATE_core().endpoint_url() + 'online/validategroupform/' + STATE_core().product_group_id() + '/?global=2&without_form=1',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_user_form
	*/
	get_user_form()
	{
		let requestParams = {
			action_name: 'getform',
			method: 'POST',
			body: null,
			url: STATE_core().endpoint_url() + 'online/getform/' + STATE_params().get_selected_product().form_id + '/?json=1',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * validate_user_form
	*/
	validate_user_form()
	{
		let requestParams = {
			action_name: 'validateform',
			method: 'POST',
			body: Functions.serialize_user_input(),
			url: STATE_core().endpoint_url() + 'online/validateform/' + STATE_params().get_selected_product().form_id + '/?without_form=1',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_global_form
	*/
	get_global_form()
	{
		let requestParams = {
			action_name: 'getgroupform',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getgroupform/' + STATE_core().product_group_id() + '/?global=1&json=1',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * validate_global_form
	*/
	validate_global_form(serialized)
	{
		let requestParams = {
			action_name: 'validategroupform',
			method: 'POST',
			body: serialized,
			url: STATE_core().endpoint_url() + 'online/validategroupform/' + STATE_core().product_group_id() + '/?global=1',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Group_user_form;
