<template>
	<div class="form-check input-button-icon">
		<input
			:class="icon_class"
			type="checkbox"
			:id="field_id"
			:name="field_id"
			v-model="value"
			true-value="1"
			false-value="0"
		>
		<label
			:for="field_id"
		>
			{{ checkbox_label }}
		</label>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			checkbox_label()
			{
				return this.field_data.label;
			},
			icon_class()
			{
				return this.field_data.wrapper_attrs;
			},
			value:
			{
				get() {
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
				},
			},
		},
		watch: {
		},
	}
</script>
