<template>
	<div
		:class="is_disabled ? 'global_form_disabled' : ''"
		class="row g-0 global_form"
		data-cy="global_form"
	>
		<!-- loop -->
		<div
			v-for="(field_data, field_id) in inputs"
			class="col-sm-4"
		>

			<!-- label -->
			<label :for="field_id" >
				{{ field_data.label }}
			</label>

			<!-- input -->
			<Checkbox
				v-if="field_data.type === 'checkbox'"
				:field_id="field_id"
				:field_data="field_data"
				emit_name="validate_global_changed"
				@validate_global_changed="validate_global_changed"
			/>
			<Select
				v-if="field_data.type === 'select'"
				:field_id="field_id"
				:field_data="field_data"
				emit_name="validate_global_changed"
				@validate_global_changed="validate_global_changed"
			/>
			<Date
				v-if="field_data.type === 'date'"
				:field_id="field_id"
				:field_data="field_data"
				emit_name="validate_global_changed"
				@validate_global_changed="validate_global_changed"
			/>

			<!-- error -->
			<div
				v-for="message in has_error(field_id)"
				class="validation-placeholder validation-message"
			>
				<span class="mark-circle mark-orange">!</span>
				{{ message }}
			</div>

		</div>
	</div>
</template>

<script>
	import Group_user_form from '@/logic/requests/Group_user_form.js';
	import Functions from '@/logic/Functions.js';
	import Checkbox from '@/components/forms/basic_inputs/Checkbox.vue';
	import Select from '@/components/forms/basic_inputs/Select.vue';
	import Date from '@/components/forms/advanced_inputs/Date.vue';
	import Product_helpers from '@/components/form_app/product/Product_helpers.js';
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
			global_disabled: Boolean,
		},
		components: {
			Checkbox,
			Select,
			Date,
		},
		emits: ['calculate_all_products'],
		data() {
			return {
				inputs: {},
				errors: {},
				validate_running: false,
			}
		},
		created() {
			// global form is not from calculate anymore, but has own getform request
			Group_user_form.get_global_form().then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					// inputs are delivered same way as step1/3 inputs, we only need simpler format here
					this.inputs = Product_helpers.product_form_formatter(response.result.form, false, false);
				}
			);
		},
		methods: {
			validate_global_changed()
			{
				this.validate_running = true;
				this.errors = {};
				// serialize only global input values from State
				let form_data = Functions.serialize_specific_inputs(Object.keys(this.inputs));
				Group_user_form.validate_global_form(form_data).then(
					(response) => {
						this.validate_running = false;
						// recalculate all if valid
						if (response.result.validform === true) {
							this.$emit('calculate_all_products');
							return;
						}
						// show errors if invalid
						this.errors = response.result.form_validation_error_elements;
					}
				);
			},
			has_error(field_id)
			{
				if (this.errors[field_id] === undefined) {
					return [];
				}
				// each input could have multiple error messages
				let messages = [];
				for (let key in this.errors[field_id]) { if (!this.errors[field_id].hasOwnProperty(key)) { continue; }
					let first_key = Object.keys(this.errors[field_id])[0];
					let value = this.errors[field_id][first_key];
					messages.push(value);
				}
				return messages;
			},
		},
		computed: {
			is_disabled()
			{
				return this.global_disabled || this.validate_running;
			},
		},
	}
</script>

<style scoped lang="scss">
	.global_form_disabled {
		pointer-events: none;
		opacity: 0.5;
	}
</style>
