<template>
	<h4>Platba cez QR kd</h4>
	<div id="qr_container">
		<img
			:src="qr_code"
			class="qr_code"
		>
	</div>
</template>

<script>
	import Payment_requests from '@/logic/requests/Payment_requests.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
				base64: '',
			}
		},
		created() {
			Payment_requests.generate_qr().then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					this.base64 = response.result.pbs;
				}
			);
		},
		methods: {
		},
		computed: {
			qr_code()
			{
				if (this.base64 === '') {
					return '';
				}
				return 'data:image/png;base64,' + this.base64;
			},
		},
		watch: {
		},
	}
</script>
