import { defineStore } from 'pinia';

export default defineStore(
	'STATE_docs_landing',
	{
		state: () => {
			return {
				session_state: [
					/*
					{
						id: 36472,
						name: "red-f-tp.jpg",
						createdAt: "2022-10-18 13:27:35",
						documentType: "image/jpeg",
						tags: [ "tp" ],
						token: "d8dc6a05-db03-40a9-98f2-fd70152eee35",
						documentState: "available",
						dmsState: "synced",
						dataRecords: [
							{
								"product_data_record_id": "2e2f9eab-0c4e-441f-b0ba-ac86618534b2",
								"product_data_record_token": "upload_kasko_faktura_foto",
								"data_record": "Upload  Kasko Faktra  foto",
								"data_record_id": "73c4f354-719f-49ce-b642-ef480bd0a18b",
								"product": null,
								"product_id": null,
								"data_record_state": "STATE_PROCESSED",
								"data_record_state_id": 100,
								"data_record_log_id": "9ba77cb5-56d8-4536-a2ea-84df76f23505",
								"created": "2022-11-03 08:01:01",
								"processed": "2022-11-03 08:01:40"
							},
							{},
						],
					},
					...
					*/
				],
				input_data: {
					/*
					tag: {
						type: String, // single | tiles
						group: String, // only for tiles, group them with button
						tags: String, // unique ID
						send: String, // DV token, only for single
						heading: String, // simple text
						image: String, // file name in our project folder, only for tiles
						required: Boolean, // only for tiles, controls send button
						multiple: Integer | NaN, // only for single, 1 allow multiple files per input, NaN or 0 dont allow, >1 defines maximum files
					},
					*/
				},
				tag_status: {
					// tag: String, // 'empty' | 'uploading' | 'uploaded' | 'failed' | 'sending' | 'sent' | 'error'
				},
				group_errors: {
					// group_name: String,
				},
				group_displayed: {
					/*
					group_name: {
						current: Integer,
						max: Integer,
						shown_tags: {
							tag: Boolean,
							tag: Boolean,
						},
					},
					group_name: {...},
					*/
				},
			};
		},
		getters: {
			one_session_state_by_tag()
			{
				const session_state = this.session_state;
				return (tag) => {
					let i, len = session_state.length;
					for (i = 0; i < len; i++) {
						if (session_state[i].tags[0] === tag) {
							return session_state[i];
						}
					}
					return null;
				}
			},
			all_session_state_by_tag()
			{
				const session_state = this.session_state;
				return (tag) => {
					let array = [];
					let i, len = session_state.length;
					for (i = 0; i < len; i++) {
						if (session_state[i].tags[0] === tag) {
							array.push(session_state[i]);
						}
					}
					return array;
				}
			},
			get_input_data()
			{
				const input_data = this.input_data;
				return (tag) => {
					return input_data[tag] || {};
				}
			},
			get_tag_status()
			{
				const tag_status = this.tag_status;
				return (tag) => {
					return tag_status[tag];
				}
			},
			get_tags_by_group()
			{
				const input_data = this.input_data;
				return (group) => {
					let array = [];
					let i, len = input_data.length;
					for (let key in input_data) {
						if (! input_data.hasOwnProperty(key)) { continue; }
						if (input_data[key].group === group) {
							array.push(key);
						}
					}
					return array;
				}
			},
			get_group_error()
			{
				const group_errors = this.group_errors;
				return (group) => {
					return group_errors[group] || '';
				}
			},
			grouped_shown_state_by_tag()
			{
				return (tag) => {
					let group_name = this.get_input_data(tag).group;
					return this.group_displayed[group_name].shown_tags[tag];
				}
			},
			grouped_all_are_shown()
			{
				return (group_name) => {
					let grp = this.group_displayed[group_name];
					if (grp === undefined) {
						return true;
					}
					return grp.current >= grp.max;
				}
			},
			grouped_tag_position()
			{
				// returns String: 'first' | 'last' | 'both' | 'neither'
				return (tag) => {
					let all_tags = this.group_displayed[ this.get_input_data(tag).group ].shown_tags;
					let shown = [];
					for (let key in all_tags) {
						if (! all_tags.hasOwnProperty(key)) { continue; }
						if (all_tags[key] === true) {
							shown.push(key);
						}
					}
					let index = shown.indexOf(tag);
					if (index === 0 && index === shown.length - 1) {
						return 'both';
					} else if (index === 0) {
						return 'first';
					} else if (index === shown.length - 1) {
						return 'last';
					} else {
						return 'neither';
					}
				}
			},
		},
		actions: {
			set_doc_session_state(documents)
			{
				this.session_state = documents;
			},
			add_input_data(tag, input)
			{
				this.input_data[tag] = input;
			},
			set_tag_status(tag, status)
			{
				let allowed = ['empty', 'uploading', 'uploaded', 'failed', 'sending', 'sent', 'error'];
				if (allowed.indexOf(status) === -1) {
					console.log('baaad status');
					return;
				}
				this.tag_status[tag] = status;
			},
			init_status_from_session(tag)
			{
				const session = this.session_state;
				let status = 'empty';
				let i, len = session.length;
				for (i = 0; i < len; i++) {
					if (session[i].tags?.[0] === tag) {
						if (typeof session[i].dataRecords === 'object' && Object.keys(session[i].dataRecords).length > 0) {
							status = 'sent';
						} else {
							status = 'uploaded';
							break; // for multiple, only one uploaded means whole status as uploaded
						}
					}
				}
				this.set_tag_status(tag, status);
			},
			add_group_error(group, error)
			{
				this.group_errors[group] = error;
			},
			setup_displayed_in_group(group_name, max, all_tags)
			{
				let setup = {
					current: 0,
					max: max,
					shown_tags: {},
				};
				let i, len = all_tags.length;
				for (i = 0; i < len; i++) {
					const session = this.one_session_state_by_tag(all_tags[i]);
					if (i === 0 || session !== null) {
						setup.shown_tags[all_tags[i]] = true;
						setup.current++;
					} else {
						setup.shown_tags[all_tags[i]] = false;
					}
				}
				this.group_displayed[group_name] = setup;
			},
			increase_input_in_group(group_name)
			{
				let grp = this.group_displayed[group_name];
				if (grp.current >= grp.max) {
					return;
				}
				this.group_displayed[group_name].current = this.group_displayed[group_name].current + 1;
				for (let key in grp.shown_tags) {
					if (! grp.shown_tags.hasOwnProperty(key)) { continue; }
					if (grp.shown_tags[key] === false) {
						this.group_displayed[group_name].shown_tags[key] = true;
						break;
					}
				}
			},
			decrease_input_in_group(tag)
			{
				let group_name = this.get_input_data(tag).group;
				if (this.group_displayed[group_name].current <= 1) {
					return;
				}
				this.group_displayed[group_name].current = this.group_displayed[group_name].current - 1;
				this.group_displayed[group_name].shown_tags[tag] = false;
			},
		},
	}
);
