<template>
	<Modal_body :close="true" title="Kame kame s touto session?" >

		<p
			v-for="info in info"
		>
			{{ info }}
		</p>

		<button
			@click="send_troops"
			:disabled="designated_location === 0"
			class="btn btn-secondary"
		>
			Navtv step {{ this.designated_location }}
		</button>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_core from '@/store/STATE_core.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import Group_user_form from '@/logic/requests/Group_user_form.js';
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
		},
		components: {
			Modal_body,
		},
		emits: [],
		data() {
			return {
				info: [],
				designated_location: 0,
			}
		},
		created() {
			Promise.resolve().then(
				() => {
					this.info.push('validategroupform');
					return Group_user_form.validate_group_form();
				}
			).then(
				(response) => {
					this.info.push('je form validny? ' + response.result.validform);
					if (response.result.validform === false) {
						this.info.push('neni. takze pojdes do step1');
						this.designated_location = 1;
						return false;
					}
					return true;
				}
			).then(
				(go) => {
					if (!go) {
						return false;
					}
					this.info.push('getdata');
					return Session_data.get_data();
				}
			).then(
				(response) => {
					if (!response) {
						return false;
					}
					this.info.push('bol vybraty produkt id: ' + response.result?.call_params?.product_id);
					STATE_params().extract_selected_product(response);
					if (STATE_params().get_selected_product().product_id === null) {
						this.info.push('takze nebol. pojdes step2.');
						this.designated_location = 2;
						return false;
					}
					return true;
				}
			).then(
				(go) => {
					if (!go) {
						return false;
					}
					this.info.push('validateform');
					return Group_user_form.validate_user_form();
				}
			).then(
				(response) => {
					if (!response) {
						return false;
					}
					this.info.push('je formular validny? ' + response.result.validform);
					if (response.result.validform === false) {
						this.info.push('neni. takze sup do step3');
						this.designated_location = 3;
						return false;
					}
					return true;
				}
			).then(
				(go) => {
					if (!go) {
						return false;
					}
					this.info.push('getsessioninfo');
					return Session_data.session_info().then(
						(response) => {
							this.info.push('stav session je: ' + response.result.state);
							if (response.result?.state < 100) {
								this.info.push('este neni confirmuta, takze do step4');
								this.designated_location = 4;
								return false;
							}
							return true;
						}
					);
				}
			).then(
				(go) => {
					if (!go) {
						return false;
					}
					this.info.push('uz je confirmuta, tak sup step5');
					this.designated_location = 5;
				}
			);
		},
		methods: {
			send_troops()
			{
				STATE_core().change_step(this.designated_location);
				STATE_modals().close_them_all();
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
