<template>
	<div class="dl_item_action">
		<div class="row">
			<div class="col-12 col-sm-9 my-auto text-center text-md-start">
				<p>
					{{ input.text }}
				</p>
			</div>
			<div class="col-12 col-sm-3 text-center text-md-end text-nowrap">
				<span
					v-if="allow_adding_more"
					@click="increase_shown"
					class="dl_add align-middle d-block d-sm-inline-block mx-auto me-sm-3 mt-2 mt-sm-0"
					title="Prida sbor"
				>
				</span>
				<button
					@click="send_to_provider"
					:disabled="send_disabled"
					type="button"
					class="btn btn-primary btn-send btn-m mt-2 mt-sm-0"
					:title="input.group"
				>
					Posla
				</button>
			</div>
		</div>
	</div>
</template>

<script>
	import Data_record from '@/logic/requests/Data_record.js';
	import STATE_docs_landing from '@/store/STATE_docs_landing.js';

	export default {
		props: {
			input: {
				send: String,
				text: String,
				group: String,
			},
		},
		components: {
		},
		data() {
			return {
			}
		},
		methods: {
			set_status(status)
			{
				var i, len = this.grouped_tags.length;
				for ( i = 0; i < len; i++ ) {
					let tag = this.grouped_tags[i];
					if (STATE_docs_landing().get_tag_status(tag) === 'empty') {
						continue;
					}
					STATE_docs_landing().set_tag_status(tag, status);
				}
			},
			send_to_provider()
			{
				let record_token = this.input.send;
				if (record_token === undefined) {
					console.log('record_token is undefined, input has no send=... parameter');
					return;
				}
				this.set_status('sending');
				Data_record.create_data_record(record_token).then(
					(response) => {
						if (response.status === 200) {
							this.set_status('sent');
							return;
						}
						STATE_docs_landing().add_group_error(this.input.group, response.result?.message);
						this.set_status('error');
					}
				);
			},
			increase_shown()
			{
				STATE_docs_landing().increase_input_in_group(this.input.group);
			},
		},
		computed: {
			grouped_tags()
			{
				return STATE_docs_landing().get_tags_by_group(this.input.group);
			},
			send_disabled()
			{
				let disabled = true;
				let i, len = this.grouped_tags.length;
				for (i = 0; i < len; i++) {
					let tag = this.grouped_tags[i];
					let status = STATE_docs_landing().get_tag_status(tag);
					let required = STATE_docs_landing().get_input_data(tag).required;
					if (required && status === 'empty') {
						disabled = true;
						break;
					}
					if (status === 'uploaded' || status === 'error') {
						disabled = false;
					}
				}
				return disabled;
			},
			allow_adding_more()
			{
				return ! STATE_docs_landing().grouped_all_are_shown(this.input.group);
			},
		},
	}
</script>
