import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';
import STATE_params from '@/store/STATE_params.js';

/**
 * Get_static_docs
*/
class Get_static_docs
{
	/**
	 * static_documents
	*/
	static_documents()
	{
		let requestParams = {
			action_name: 'getdocuments',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getdocuments/' + STATE_params().get_selected_product().product_id + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_static_document
	*/
	get_static_document(document_id)
	{
		let requestParams = {
			action_name: 'getdocument',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getdocument/' + STATE_params().get_selected_product().product_id + '/' + document_id + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Get_static_docs;
