<template>
	<section data-cy="info">
		<small>step</small>
		<h2 class="mb-0 d-inline-block">{{ heading }}</h2>
		<p class="mx-4 d-inline-block">{{ print_session_token }}</p>
		<p class="mx-4 d-inline-block">{{ print_agent_code }}</p>
		<p class="mx-4 d-inline-block">{{ print_filtered_products }}</p>
	</section>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			heading() {
				var id = STATE_core().product_group_id();
				var groups = {
					'1': 'Povinn zmluvn poistenie',
					'2': 'Havarijn poistenie',
					'3': 'Hypotekrny ver',
					'4': 'ivotn poistenie',
					'5': 'GAP poistenie finannej straty',
					'6': 'Investcie',
					'7': 'Cestovn poistenie',
					'8': 'Poistenie zodpovednosti zamestnanca',
					'9': 'Poistenie majetku - byt, dom, domcnos',
					'10': 'Poistenie podnikateov',
					'72': 'Sedadl',
					'82': 'Test',
					'73': 'Zdravotn poistenie cudzincov',
					'85': 'Starobn dchodkov sporenie',
					'86': 'Doplnkov dchodkov sporenie',
				};
				return STATE_core().current_step() + ', ' + groups[id];
			},
			print_session_token()
			{
				return STATE_core().get_session_token();
			},
			print_agent_code()
			{
				let code = STATE_params().get_agent_code();
				return code === '' ? '-' : code;
			},
			print_filtered_products()
			{
				let filtered = STATE_params().get_filtered_products();
				return filtered === '' ? '-' : filtered;
			},
		},
		watch: {
		},
	}
</script>
