import Request_service from '@/logic/Request_service.js';
// import State from '@/store/State.js';

/**
 * Get_flow_html
*/
class Get_flow_html
{
	/**
	 * osmapi_get_flow_html
	*/
	osmapi_get_flow_html(action)
	{
		let requestParams = {
			action_name: 'get-html',
			method: 'GET',
			url: State.endpoint_url() + 'osmapi/get-html/' + State.get_session_token(),
		};

		var array = [];
		array.push('template=' + action.functionParams.template);
		// array.push('action-id=' + action.id);
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		var req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Get_flow_html;
