<template>
	<div
		v-show="is_displayed_in_group"
		:class="sii ? [] : ['tiles dl_item_file d-flex flex-column text-center', class_status]"
	>
		<div
			v-if="dgg_only_first"
			:class="sii ? 'dl_title text-center text-md-start' : 'dl_title text-center'"
		>
			<h5>
				{{ heading_and_required }}
			</h5>
		</div>
		<div
			:class="sii ? ['dl_item_file d-flex flex-column flex-md-row align-items-stretch text-center text-md-start', class_status, dgg_not_last] : ['d-flex flex-column']"
		>
			<div :class="sii ? 'dl_image d-flex pb-2 pt-3 p-md-2 mx-auto mx-md-0 mx-auto mx-md-0' : 'dl_image px-3 py-2'">
				<div class="img_wrapper">
					<img
						v-if="sii"
						:src="image_sample"
						width="67" height="67"
						class="my-auto"
					>
					<img
						v-else
						:src="image_sample"
						width="322" height="241.5"
					>
				</div>
			</div>
			<div
				v-if="sii"
				class="dl_filename pt-1 pb-0 py-md-4"
			>
				<p
					v-if="mff"
					class="my-auto mx-auto mx-md-0"
					:title="give_filename.names.join(' ')"
				>
					{{ give_filename.label }}
				</p>
				<p
					v-else
					class="my-auto mx-auto mx-md-0"
				>
					{{ give_filename }}
				</p>
			</div>
			<div :class="sii ? 'dl_up_download py-3 py-md-4 px-3' : 'dl_up_download py-3'">
				<input
					@change="picked_file"
					:multiple="mff"
					ref="file_input"
					type="file"
					id="{{ input.tags }}"
				>
				<div :class="sii ? 'btn-wrapper my-auto mx-auto mx-md-0' : 'btn-wrapper mx-auto'">
					<button
						@click="select_file"
						type="button"
						class="btn btn-primary btn-upload btn-m"
						title="Nahra sbor"
					>
						{{ mff ? 'Vyberte sbory' : 'Vyberte sbor' }}
					</button>
					<button
						@click="download_doc"
						type="button"
						class="btn btn-primary btn-download btn-m btn-compact"
						:class="allow_download ? '' : 'v_hidden'"
						title="Stiahnu sbor"
					>
						<!-- Stiahnu sbor -->
					</button>
				</div>
			</div>
			<div class="dl_progress">
				<div
					:style="{ height: number_solid + '%' }"
					class="dl_bar bar_vertical"
				></div>
				<div
					:style="{ height: number_opaque + '%', opacity: '0.3', }"
					class="dl_bar bar_vertical"
				></div>
				<div
					:style="{ width: number_solid + '%' }"
					class="dl_bar bar_horizontal"
				></div>
				<div
					:style="{ width: number_opaque + '%', opacity: '0.3', }"
					class="dl_bar bar_horizontal"
				></div>
			</div>
			<div :class="sii ? 'dl_status pt-3 pb-2 py-md-3 py-md-4 ps-md-3' : 'dl_status py-4 py-md-3 px-3'">
				<p
					:class="sii ? 'my-auto mx-auto mx-md-0' : 'my-auto mx-auto'"
				>
					{{ user_info_status }}
				</p>
				<div
					v-if="mff"
					v-for="(value, key) in preview_many"
					:title="key + (value.allow_delete ? '' : ' ODOSLAN')"
					class="cross_on_hover"
				>
					<div
						v-if="value.allow_delete"
						@click="delete_doc(key)"
						class="overlay"
					>
						<div class="delete_symbol"></div>
					</div>
					<img
						v-if="value.src === 'pdf_icon'"
						src="@/assets/images/common/dl_img_doc_general.svg"
						width="41" height="41"
					>
					<img
						v-else
						:src="value.src"
						width="41" height="41"
					>
				</div>
			</div>
			<div
				v-if="sii && !dgg"
				class="dl_action pt-2 pb-3 py-md-3 py-md-4 px-3"
			>
				<button
					@click="send_to_provider"
					:disabled="send_is_disabled"
					type="button"
					class="btn btn-primary btn-send btn-m my-auto mx-auto mx-md-0"
				>
					Posla
				</button>
			</div>
			<div
				v-if="show_minus"
				class="dl_action pt-2 pb-3 py-md-3 py-md-4 pe-3"
			>
				<span
					@click="decrease_shown"
					class="dl_remove mx-auto mx-md-0 my-md-auto"
					title="Odobra sbor"
				>
				</span>
			</div>
		</div>
	</div>
</template>

<script>
	import Get_session_docs from '@/logic/requests/Get_session_docs.js';
	import Data_record from '@/logic/requests/Data_record.js';
	import Functions from '@/logic/Functions.js';
	import STATE_docs_landing from '@/store/STATE_docs_landing.js';

	export default {
		props: {
			tag: String, // unique identifier for input
		},
		components: {
		},
		emits: ['refresh_uploaded_ids'],
		data() {
			return {
				debug: false,
				error_message: '',
				preview_one: '',
				preview_many: {
					// src: '',
					// allow_delete: false,
				},
				number_solid: 0,
				number_opaque: 0,
				number_increment: 0,
			}
		},
		created() {
			STATE_docs_landing().init_status_from_session(this.tag);
			this.download_preview();
		},
		methods: {
			select_file()
			{
				this.$refs.file_input.click();
			},
			set_status(status)
			{
				STATE_docs_landing().set_tag_status(this.tag, status);
			},
			picked_file(event)
			{
				this.set_status('uploading');
				if (this.validate_files_stop_upload(event.target.files)) {
					this.set_status('failed');
					return;
				}
				for (let key in event.target.files) {
					if (! event.target.files.hasOwnProperty(key)) { continue; }
					let payload = this.prepare_payload(event.target.files[key]);
					this.upload_doc(payload);
				}
				this.$refs.file_input.value = null; // clear chrome bug
			},
			validate_files_stop_upload(files)
			{
				// too many files
				if (this.mff && this.input_data.multiple > 1) {
					let picked = files.length;
					let uploaded = this.get_all_session.length;
					let max = this.input_data.multiple;
					if (picked + uploaded > max) {
						this.error_message = 'Presiahnut maximlne mnostvo sborov, vybran: ' + picked + ' nahran: ' + uploaded + ', max: ' + max;
						return true;
					}
				}
				// too big files
				let max_mb = 2; // megabytes
				let bites = 1024 * 1024;
				for (let key in files) {
					if (! files.hasOwnProperty(key)) { continue; }
					let file = files[key];
					if (file.size > max_mb * bites) {
						this.error_message = 'Presiahnut povolen vekos sboru ' + (Math.round((file.size / bites * 100)) / 100) + 'mb, max: ' + max_mb + 'mb';
						return true;
					}
				}
				return false;
			},
			prepare_payload(file)
			{
				let filename = Functions.slugify_keep_extension_add_tag(file.name, this.tag);
				if (Functions.starts_with(file.type, 'image/')) {
					if (this.mff) {
						this.preview_many[filename] = {
							allow_delete: true,
							src: URL.createObjectURL(file),
						};
					} else {
						this.preview_one = URL.createObjectURL(file);
					}
				} else {
					if (this.mff) {
						this.preview_many[filename] = {
							allow_delete: true,
							src: 'pdf_icon'
						};
					}
				}
				let payload = new FormData();
				payload.append('file', file, filename);
				payload.append('title', filename);
				payload.append('description', 'uploaded by docs landing page');
				payload.append('type', file.type);
				payload.append('tags', this.tag);
				if (!this.mff && this.get_one_session !== null) {
					payload.append('id', this.get_one_session.id);
				}
				if (this.debug) {
					let log = [];
					for (let item of payload) {
						log.push(item);
					}
					console.log('PAYLOAD:', log);
				}
				return payload;
			},
			upload_doc(payload)
			{
				Get_session_docs.upload_session_document(payload).then(
					(response) => {
						// zmen stav len ak neni mff
						if (response.status === 200) {
							this.set_status('uploaded');
							this.$emit('refresh_uploaded_ids');
							return;
						}
						this.set_status('failed');
						console.log(response);
					}
				);
			},
			download_doc()
			{
				// download kazdy zvlast
				let sessions = [];
				if (this.mff) {
					sessions = this.get_all_session;
				} else {
					sessions = [this.get_one_session];
				}
				let i, len = sessions.length;
				for (i = 0; i < len; i++) {
					let name = sessions[i].name;
					let id = sessions[i].id;
					let type = sessions[i].documentType;
					Get_session_docs.get_session_document(id).then(
						(response) => {
							if (response.status === 200) {
								Functions.downloadBase64(name, response.result.content, null, type);
								return;
							}
							this.error_message = response.result?.message;
							this.set_status('failed');
						}
					);
				}
			},
			delete_doc(filename)
			{
				let sessions = this.get_all_session;
				let id = undefined;
				for (var i = sessions.length - 1; i >= 0; i--) {
					if (sessions[i].name === filename) {
						id = sessions[i].id;
					}
				}
				if (id === undefined) {
					console.log('this should not happen, we dont have ID for ' + filename);
					return;
				}
				Get_session_docs.delete_session_document(id).then(
					(response) => {
						if (response.status !== 200) {
							return;
						}
						delete this.preview_many[filename];
						if (Object.keys(this.preview_many).length === 0) {
							this.set_status('empty');
						}
						this.$emit('refresh_uploaded_ids');
					}
				);
			},
			download_preview()
			{
				if (this.get_status !== 'sent' && this.get_status !== 'uploaded') {
					return;
				}
				this.number_solid = 100;
				let size = this.sii ? 100 : 350; // square size in px
				if (this.mff) {
					let session = this.get_all_session;
					let i, len = session.length;
					for (i = len-1; i >= 0; i--) {
						let name = session[i].name;
						let id = session[i].id;
						let type = session[i].documentType;
						let dv = session[i].dataRecords;
						if (!Functions.starts_with(type, 'image/')) {
							this.preview_many[name] = {
								allow_delete: (dv.length === 0) ? true : false,
								src: 'pdf_icon'
							};
							continue;
						}
						Get_session_docs.get_session_document_preview(id, size).then(
							(response) => {
								if (response.result.content !== undefined) {
									this.preview_many[name] = {
										allow_delete: (dv.length === 0) ? true : false,
										src: 'data:image/jpeg;charset=utf-8;base64,' + response.result.content,
									};
								}
							}
						);
					}
					return;
				}
				let name = this.get_one_session.name;
				let id = this.get_one_session.id;
				let type = this.get_one_session.documentType;
				if (!Functions.starts_with(type, 'image/')) {
					return;
				}
				Get_session_docs.get_session_document_preview(id, size).then(
					(response) => {
						this.preview_one = 'data:image/jpeg;charset=utf-8;base64,' + response.result.content;
					}
				);
			},
			send_to_provider()
			{
				let record_token = this.input_data.send;
				if (record_token === undefined) {
					console.log('record_token is undefined, input has no send=... parameter');
					return;
				}
				this.set_status('sending');
				Data_record.create_data_record(record_token).then(
					(response) => {
						if (response.status === 200) {
							this.forbid_deletation_for_sent();
							this.set_status('sent');
							return;
						}
						this.error_message = response.result?.message;
						this.set_status('error');
					}
				);
			},
			forbid_deletation_for_sent()
			{
				if (!this.mff) {
					return;
				}
				for (let key in this.preview_many) {
					if (! this.preview_many.hasOwnProperty(key)) { continue; }
					this.preview_many[key].allow_delete = false;
				}
			},
			progress_bar_loading()
			{
				this.number_solid = 0;
				this.number_opaque = 0;
				this.number_increment = 2;
				let faster = setInterval(
					() => {
						this.number_opaque = this.number_opaque + 2;
						if (this.number_opaque >= 100) {
							this.number_opaque = this.number_solid - 10;
						}
					},
					10
				);
				let interval = setInterval(
					() => {
						if (this.get_status === 'uploaded' || this.get_status === 'sent') {
							this.number_increment = 5;
						}
						if (this.number_increment > 0) {
							this.number_increment = this.number_increment - 0.03;
						} else {
							this.number_increment = 0;
						}
						this.number_solid = this.number_solid + this.number_increment;
						if (this.number_solid >= 100 || this.get_status === 'failed' || this.get_status === 'error') {
							this.number_solid = 100;
							clearInterval(interval);
							clearInterval(faster);
						}
					},
					50
				);
			},
			decrease_shown()
			{
				STATE_docs_landing().decrease_input_in_group(this.tag);
				let session = this.get_one_session;
				if (session === null) {
					return;
				}
				Get_session_docs.delete_session_document(session.id).then(
					(response) => {
						if (response.status !== 200) {
							return;
						}
						this.preview_one = '';
						this.number_solid = 0;
						this.number_opaque = 0;
						this.set_status('empty');
						this.$emit('refresh_uploaded_ids');
					}
				);


			},
		},
		computed: {
			get_status()
			{
				return STATE_docs_landing().get_tag_status(this.tag);
			},
			input_data()
			{
				return STATE_docs_landing().get_input_data(this.tag);
			},
			get_one_session()
			{
				return STATE_docs_landing().one_session_state_by_tag(this.tag);
			},
			get_all_session()
			{
				return STATE_docs_landing().all_session_state_by_tag(this.tag);
			},
			send_is_disabled()
			{
				return this.get_status === 'uploaded' || this.get_status === 'error' ? false : true;
			},
			user_info_status()
			{
				let info = {
					empty: 'Nahrajte prosm prlohu',
					uploading: 'Nahrvam...',
					uploaded: this.mff ? 'sbory nahrat' : 'sbor nahrat',
					failed: this.error_message === '' ? 'nahrvanie zlyhalo' : this.error_message,
					sending: 'Odosielam...',
					sent: this.mff ? 'sbory odoslan do poisovne' : ('odoslan do poisovne ' + Functions.format_docs_date(this.get_one_session?.dataRecords[0]?.processed)),
					error: this.sioo ? this.error_message : STATE_docs_landing().get_group_error(this.input_data.group),
				};
				return info[this.get_status];
			},
			class_status()
			{
				let classes = {
					empty: '',
					uploading: '',
					uploaded: 'dl_success',
					failed: 'dl_warning',
					sending: '',
					sent: 'dl_sent',
					error: 'dl_warning',
				};
				return classes[this.get_status];
			},
			allow_download()
			{
				if (this.get_status === 'empty' || this.get_status === 'uploading' || this.get_status === 'sending') {
					return false;
				}
				return true;
			},
			sioo()
			{
				// really single input only
				return this.input_data.type === 'single';
			},
			sii()
			{
				// single input instance, not tiles
				return (this.input_data.type === 'single' || this.input_data.type === 'grouped');
			},
			mff()
			{
				// multiple files fuckery, allow multiple files
				return (isNaN(this.input_data.multiple) || this.input_data.multiple === 0) ? false : true;
			},
			dgg()
			{
				// dynamic group gang
				return this.input_data.type === 'grouped';
			},
			heading_and_required()
			{
				return this.input_data.heading + (this.input_data.required ? ' *' : '');
			},
			dgg_only_first()
			{
				// show title for all inputs, but for DGG only for first
				if (!this.dgg) {
					return true;
				}
				let grouped_position = STATE_docs_landing().grouped_tag_position(this.tag);
				return grouped_position === 'first' || grouped_position === 'both';
			},
			dgg_not_last()
			{
				// adjust margin for all DGG inputs, except last
				if (!this.dgg) {
					return false;
				}
				let grouped_position = STATE_docs_landing().grouped_tag_position(this.tag);
				return (grouped_position !== 'last' && grouped_position !== 'both') ? 'grouped_not_last' : '';
			},
			image_sample()
			{
				if (this.preview_one !== '') {
					return this.preview_one;
				}
				if (this.sii) {
					return require('@/assets/images/common/dl_img_doc_general.svg');
				}
				try {
					return require('@/assets/images/docs_landing_samples/' + this.input_data.image + '.jpg');
				} catch (error) {
					return require('@/assets/images/docs_landing_samples/placeholder_b.jpg');
				}
			},
			give_filename()
			{
				if (!this.mff) {
					return this.get_one_session?.name;
				}
				let session = this.get_all_session;
				let multiple = {
					label: '',
					names: [],
				};
				if (session.length === 1) {
					multiple.label = session.length + ' sbor';
				} else if (session.length > 1 && session.length < 5) {
					multiple.label = session.length + ' sbory';
				} else if (session.length >= 5) {
					multiple.label = session.length + ' sborov';
				}
				let i, len = session.length;
				for (i = 0; i < len; i++) {
					multiple.names.push(session[i].name);
				}
				return multiple;
			},
			is_displayed_in_group()
			{
				if (!this.dgg) {
					return true;
				}
				return STATE_docs_landing().grouped_shown_state_by_tag(this.tag);
			},
			show_minus()
			{
				if (!this.dgg) {
					return false;
				}
				let grouped_position = STATE_docs_landing().grouped_tag_position(this.tag);
				if (grouped_position === 'both') {
					return false;
				}
				if (this.get_status === 'empty' || this.get_status === 'uploaded') {
					return true;
				}
				return false;
			},
		},
		watch: {
			get_status()
			{
				if (this.get_status === 'uploading' || this.get_status === 'sending') {
					this.progress_bar_loading();
				}
			},
		},
	}
</script>
