<template>
	<div
		@click="did_click_outside"
		class="vue_modal"
		:class="huge ? 'huge' : ''"
		data-cy="modal"
	>
		<div class="modal-content">
			<div class="modal-header">
				<h4>
					{{ title }}
				</h4>
				<span
					v-if="close"
					@click="close_me"
					class="i_close"
				>
				</span>
			</div>
			<div class="modal-body">

					<slot></slot>

			</div>
		</div>
	</div>
</template>

<script>
	import STATE_modals from '@/store/STATE_modals.js';

	export default {
		props: {
			close: Boolean,
			title: String,
			huge: Boolean, // optional
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
			// close modal on Esc
			document.addEventListener(
				'keyup',
				(event) => {
					if (event.keyCode === 27) {
						this.close_me();
					}
				}
			);
		},
		methods: {
			close_me()
			{
				if (!this.close) {
					return;
				}
				STATE_modals().close_them_all();
			},
			did_click_outside(event)
			{
				if (event.target.classList.contains('vue_modal')) {
					this.close_me();
				}
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>

