import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Payment_requests
*/
class Payment_requests
{
	/**
	 * payment_info
	*/
	payment_info()
	{
		let requestParams = {
			action_name: 'getpaymentinfo',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getpaymentinfo/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * generate_qr
	*/
	generate_qr()
	{
		let requestParams = {
			action_name: 'getpaymentqr',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getpaymentqr/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_gateways
	*/
	get_gateways()
	{
		let requestParams = {
			action_name: 'getpaymentgateways',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getpaymentgateways/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * payment_create
	*/
	payment_create(id)
	{
		let requestParams = {
			action_name: 'payment-create',
			method: 'POST',
			url: STATE_core().endpoint_url() + 'online/payment-create/' + id + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * payment_result
	*/
	payment_result(id, result)
	{
		let requestParams = {
			action_name: 'payment-result',
			method: 'PUT',
			url: STATE_core().endpoint_url() + 'online/payment-result/' + id + '/?result=' + result + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Payment_requests;
