import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Remap
*/
class Remap
{
	/**
	 * session_remap
	*/
	session_remap()
	{
		let requestParams = {
			action_name: 'session-remap',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/session-remap/?destGroup=2&' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then().catch( (error) => { throw error; } );
	}
}

export default new Remap;
