<template>
	<div class="row my-3">
		<div class="col-12">
			<div v-html="print_html"></div>
		</div>
	</div>
</template>

<script>
	import Get_flow_html from '@/logic/flow_only/Get_flow_html.js';

	export default {
		props: {
			action: Object,
		},
		components: {
		},
		data() {
			return {
				html: '',
			}
		},
		mounted() {
			Get_flow_html.osmapi_get_flow_html(this.action).then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					this.html = response.result.data.resultData.html;
				}
			);
		},
		methods: {
		},
		computed: {
			print_html() {
				return this.html;
			},
		},
	}
</script>
