<template>
	<div class="input-autocomplete">
		<input
			:id="field_id"
			:name="field_id"
			:placeholder="field_data.placeholder"
			v-model="value"
			@keyup="psc_user_worker"
			@keydown.down="move_down"
			@keydown.up="move_up"
			@keydown.enter="enter_selection"
			@change="store_changed_psc"
			ref="psc_input"
			type="text"
			class="form-control"
		>
		<ul
			v-show="shown"
		>
			<li
				v-if="loading"
			>
				Natavam...
			</li>
			<li
				v-else
				v-for="(value, order) in filtered_options"
				:class="((order === selected) ? 'psc_active psc_list' : 'psc_list')"
				@click="click_selection(value)"
			>
				{{ value }}
			</li>
		</ul>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Functions from '@/logic/Functions.js';
	import Reference_table from '@/logic/requests/Reference_table.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		data() {
			return {
				value: '',
				options_stored: false,
				loading: false,
				options: [
					// '84102 Bratislava',
					// '98798 Poltr',
				],
				selected: -1,
				shown: false,
			}
		},
		created() {
			this.value = this.get_session_value;
		},
		methods: {
			psc_user_worker(event)
			{
				let code = event?.keyCode;
				let length = this.value.length;
				if (
					(code < 48 || code > 106) // numbers and letters
					&& code !== 229 // android chrome bug
					&& code !== 8 // backspace
					&& code !== undefined // filled from ICO request
				) {
					return;
				}
				if (length < 3) {
					this.options_stored = false;
					return;
				}
				if (this.options_stored === false) {
					this.options_stored = true;
					this.get_psc_options();
				}
				this.shown = true;
				// autopick if only one option, not on backspace
				if (code === 8) {
					return;
				}
				setTimeout(
					() => {
						if (this.filtered_options.length === 1) {
							this.value = this.filtered_options[0];
							this.shown = false;
						}
					},
					1000
				);
			},
			get_psc_options()
			{
				this.loading = true;
				Reference_table.psc_table(this.value).then(
					(response) => {
						this.loading = false;
						if (response.status !== 200) {
							return;
						}
						let data = response.result.data;
						this.options = [];
						for (let key in data) { if (!data.hasOwnProperty(key)) { continue; }
							this.options.push(data[key]);
						}
						// chrome bugged out focus after disabling input
						this.$nextTick(
							() => {
								this.$refs.psc_input.focus();
							}
						);
					}
				);
			},
			move_down()
			{
				this.selected++;
				if (this.selected > this.filtered_options.length - 1) {
					this.selected = 0;
				}
			},
			move_up()
			{
				this.selected--;
				if (this.selected < 0) {
					this.selected = this.filtered_options.length - 1;
				}
			},
			enter_selection()
			{
				if (this.selected === -1) {
					return;
				}
				this.value = this.filtered_options[ this.selected ];
				this.shown = false;
				STATE_params().set_form_value(this.field_id, this.value);
			},
			click_selection(value)
			{
				this.value = value;
				this.shown = false;
				STATE_params().set_form_value(this.field_id, this.value);
			},
			store_changed_psc()
			{
				STATE_params().set_form_value(this.field_id, this.value);
			},
		},
		computed: {
			filtered_options()
			{
				// user input occurs in server options
				let filtered = [];
				let i, len = this.options.length;
				for (i = 0; i < len; i++) {
					let haystack = Functions.slugify(this.options[i]);
					let needle = Functions.slugify(this.value);
					if (haystack.indexOf(needle) !== -1) {
						filtered.push(this.options[i]);
					}
				}
				return filtered;
			},
			get_session_value()
			{
				return STATE_params().get_form_value(this.field_id);
			},
		},
		watch: {
			get_session_value()
			{
				// make request and open options when new value is delivered from ICO
				if (this.value === this.get_session_value) {
					return;
				}
				this.value = this.get_session_value;
				this.options_stored = false;
				this.psc_user_worker();
			},
		},
	}
</script>
