<template>
	<Modal_body :close="true" :title="title" :huge="true" >

		<div
			v-html="html"
			class="product_detail_html"
		></div>

		<div class="docs_download">
			<Document_download v-for="(value, key) in static_docs" :doc="value" />
		</div>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Get_static_docs from '@/logic/requests/Get_static_docs.js';
	import Document_download from '@/components/form_app/documents/Document_download.vue';

	export default {
		props: {
		},
		components: {
			Modal_body,
			Document_download,
		},
		emits: [],
		data() {
			return {
				title: '',
				html: '',
				static_docs: {},
			}
		},
		created() {
			let detail = STATE_modals().retrieve_modal_data();
			this.title = detail.product_name;
			Get_form_html.get_it(detail.detail_template).then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					this.html = response.result.html;
					this.add_tab_listeners();
				}
			);
			Get_static_docs.static_documents().then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					this.static_docs = response.result.documents;
				}
			);
		},
		methods: {
			add_tab_listeners()
			{
				// has to run with timeout so browser do this after printing html
				setTimeout(
					() => {
						// tabs
						let tab_pane = document.querySelectorAll('#osm_app .vue_modal .tab-pane');
						for (let key in tab_pane) { if (!tab_pane.hasOwnProperty(key)) { continue; }
							// remove BS classes
							tab_pane[key].classList.remove('fade', 'in');
							// fix wrong nesting
							if (tab_pane[key].parentNode.classList.contains('tab-pane')) {
								tab_pane[key].parentNode.parentNode.appendChild(tab_pane[key]);
							}
						}
						// header nav
						let nav = document.querySelectorAll('#osm_app .vue_modal ul.nav-tabs a');
						for (let key in nav) { if (!nav.hasOwnProperty(key)) { continue; }
							nav[key].addEventListener(
								'click',
								(event) => {
									// remove href event
									event.preventDefault();
									// find buttons target
									let href = event.target.hash.replace('#', '');
									for (let index in tab_pane) { if (!tab_pane.hasOwnProperty(index)) { continue; }
										// hide all tabs
										tab_pane[index].classList.remove('active');
										nav[index].parentNode.classList.remove('active');
										// show targeted tab
										if (tab_pane[index].id === href) {
											tab_pane[index].classList.add('active');
											nav[index].parentNode.classList.add('active');
										}
									}
								}
							);
						}
						// body nav
						let link = document.querySelectorAll('#osm_app .vue_modal .product_detail_tab_switcher');
						for (let key in link) { if (!link.hasOwnProperty(key)) { continue; }
							let show = link[key].dataset.show;
							let scroll = link[key].dataset.scroll;
							link[key].addEventListener(
								'click',
								(event) => {
									event.preventDefault();
									// switch tab
									for (let index in tab_pane) { if (!tab_pane.hasOwnProperty(index)) { continue; }
										// hide all tabs
										tab_pane[index].classList.remove('active');
										nav[index].parentNode.classList.remove('active');
										// show targeted tab
										if (parseInt(index, 10) === parseInt(show, 10) - 1) {
											tab_pane[index].classList.add('active');
											nav[index].parentNode.classList.add('active');
										}
									}
									// scroll to element
									let span = document.querySelector('#osm_app .vue_modal .' + scroll);
									if (span === null) {
										return;
									}
									span.scrollIntoView();
								}
							);
						}
						// end
					},
					100
				);
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>

<style lang="scss" scoped>
	// disable theft, text cant be selected
	.product_detail_html {
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
	}
</style>
