<template>
	<section>

		<Title
			title="Gratulujeme"
			subtitle="Nvrh zmluvy bol spene vytvoren a zaslan poskytovateovi."
		>
			<p class="mb-5">
				Spltku uhrate <strong class="text-accent">ete dnes</strong>.
				<strong v-if="add_what_next" class="text-highlight">
					Bez zaplatenia zmluva nevznikne.
				</strong>
			</p>
		</Title>

		<div class="row gx-3 gy-3">

			<!-- 1 payment -->
			<Point
				:order="1"
				:title="point_one_title"
			>
				<p
					v-if="dds"
					v-html="html.view_confirm_payment"
					class="mt-4 mb-5 mx-4"
				></p>
				<div
					v-else
					class="row"
				>
					<div class="col-md-4 mb-3 mb-md-0">
						<!-- payment gateways -->
						<Gateways />
					</div>
					<div class="col-sm-6 col-md-4 mb-3 mb-md-0">
						<!-- payment info -->
						<Info />
					</div>
					<div class="col-sm-6 col-md-4">
						<!-- payment qr code -->
						<Qr />
					</div>
				</div>
			</Point>

			<!-- 2 documents -->
			<Point
				:order="2"
				title="Uloi dokumenty"
			>
				<Documents_prepare
					:start_doc_requests="start_doc_requests"
					step_html="5"
				/>
			</Point>

			<!-- 3 next steps -->
			<Point
				:order="3"
				title="E-mail a alie intrukcie"
			>
				<div
					v-html="html.view_confirm_text"
				></div>
			</Point>

			<!-- TODOOOOOOOOOOOOOOO -->
			<div class="col-12 px-0 mt-5">
				<div id="docs_landing_in_step5">
					<!-- <div id="osm_app" data-version="0.3.11"></div> -->
					<!-- replaced via js -->
				</div>
			</div>

		</div>
	</section>

	<!-- remap existing session data into other new session -->
	<Remap v-if="remap_active" />

</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Documents_prepare from '@/components/form_app/documents/Documents_prepare.vue';
	import Gateways from '@/components/form_app/payment/Gateways.vue';
	import Info from '@/components/form_app/payment/Info.vue';
	import Qr from '@/components/form_app/payment/Qr.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_params from '@/store/STATE_params.js';
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Remap from '@/components/form_app/payment/Remap.vue';
	import Title from '@/components/form_app/payment/html_slots/Title.vue';
	import Point from '@/components/form_app/payment/html_slots/Point.vue';

	export default {
		props: {
		},
		components: {
			Documents_prepare,
			Gateways,
			Info,
			Qr,
			Remap,
			Title,
			Point,
		},
		emits: [],
		data() {
			return {
				group: 0,
				receiver: 0,
				html: {
					view_confirm_payment: '',
					view_confirm_text: '',
				},
				start_doc_requests: false,
			}
		},
		created() {
			// save group id for wording filters
			this.group = STATE_core().product_group_id();
			// html templates
			Get_form_html.get_it('view_confirm_text').then(
				(response) => {
					this.html.view_confirm_text = response.result?.html;
				}
			);
			Get_form_html.get_it('view_confirm_payment').then(
				(response) => {
					this.html.view_confirm_payment = response.result?.html;
				}
			);
			// get_data for selected_product which is needed for documents
			Promise.resolve().then(
				() => {
					return Session_data.get_data();
				}
			).then(
				(response) => {
					STATE_params().extract_selected_product(response);
					// save receiver for remap
					this.receiver = parseInt(response.result?.call_params?.receiver, 10);
					// start document requests
					this.start_doc_requests = true;
				}
			);
		},
		methods: {
		},
		computed: {
			add_what_next()
			{
				return this.group !== 4/*zivotne*/;
			},
			dds()
			{
				// dss and dds and cudzinci and hypo groups have different content
				return this.group === 85/*dss*/ || this.group === 86/*dds*/ || this.group === 73/*cudzinci*/ || this.group === 3/*hypoteky*/;
			},
			remap_active()
			{
				return this.receiver === 2/*finport*/ && this.group === 1/*pzp*/;
			},
			point_one_title()
			{
				if (this.group === 85/*dss*/ || this.group === 86/*dds*/ || this.group === 3/*hypoteky*/) {
					return 'Podpsa vygenerovan dokumentciu';
				} else if (this.group === 6/*investicie*/) {
					return 'Uhradi na et finannej intitcie';
				} else {
					return 'Uhradi na et poisovne';
				}
			},
		},
		watch: {
		},
	}
</script>
