<template>
	<div class="slider_scope">
		<div
			:id="field_id"
			ref="slider"
		></div>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import * as noUiSlider from 'nouislider';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			changed() {
				let event_value = this.$refs.slider.noUiSlider.get();
				STATE_params().set_form_value(this.field_id, event_value);
			},
		},
		computed: {
		},
		mounted() {
			let input_value = STATE_params().get_form_value(this.field_id);
			// data-minvalue="0" data-maxvalue="100" data-fromvalue="0" data-step="1"
			let slider_attributes = this.field_data.wrapper_attrs.split( 'value' ).join( '' );
			let min = slider_attributes.match(/min="(.*?)"/)?.[0].replace('min="', '').replace('"', '');
			let max = slider_attributes.match(/max="(.*?)"/)?.[0].replace('max="', '').replace('"', '');
			let step = slider_attributes.match(/step="(.*?)"/)?.[0].replace('step="', '').replace('"', '') || 1;

			noUiSlider.create(
				this.$refs.slider,
				{
					start: input_value,
					connect: true,
					tooltips: [true],
					step: parseInt(step, 10),
					range: {
						'min': parseInt(min, 10),
						'max': parseInt(max, 10),
					},
					// pips: {
					// 	mode: 'range',
					// 	density: 3,
					// },
					format: {
						// 'to' the formatted value. Receives a number.
						to: function (value) {
							return value;
						},
						// 'from' the formatted value. Receives a string, should return a number.
						from: function (value) {
							return value;
						}
					}
				}
			);

			// save value on change
			this.$refs.slider.noUiSlider.on('change', () => { this.changed() });
		},
		watch: {
		},
	}
</script>

<style scoped lang="scss">
	// disables display: flex; from parent div.input-area
	.slider_scope {
		flex-basis: 100%;
	}
	.slider_scope::v-deep() {
		@import '@/assets/libraries/nouislider_5.15.0/nouislider.scss'
	}
</style>
