<template>
	<section class="x-sell" data-cy="remap">

		<!-- html template -->
		<div
			v-html="view_redirect_text"
			class="text-center text-sm-start"
		></div>

		<!-- button -->
		<button
			@click="run_remap"
			class="btn btn-primary btn-right btn-lg"
		>
			Porovna havarijn poistenie
		</button>

		<!-- error -->
		<p v-show="remap_error">
			FAILED:<br>
			{{ remap_error }}
		</p>

	</section>
</template>

<script>
	import Get_form_html from '@/logic/requests/Get_form_html.js';
	import Remap from '@/logic/requests/Remap.js';
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
				view_redirect_text: '',
				remap_error: '',
			}
		},
		created() {
			Get_form_html.get_it('view_redirect_text').then(
				(response) => {
					this.view_redirect_text = response.result?.html;
				}
			);
		},
		methods: {
			run_remap()
			{
				this.remap_error = '';
				Remap.session_remap().then(
					(response) => {
						if (response.status !== 200) {
							this.remap_error = response.result.message;
							return;
						}
						let token = response.result.new_token;
						if (token === undefined || token === null || token === '') {
							this.remap_error = JSON.stringify(response.result);
							return;
						}
						// on succes, change session_token, go to step1, change group to 2 (HP)
						STATE_core().set_session_token(token, true);
						STATE_core().change_step(1);
						STATE_core().change_product_group_id(2);
					}
				)
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>

<style land="css">
	/*keep until full transition into SPA template*/
	.x-sell #remap_button {
		display: none;
	}
</style>
