<template>
	<select
		:id="field_id"
		:name="field_id"
		v-model="value"
		:disabled="is_disabled"
		class="form-control"
	>
		<option
			v-if="is_dynamic_select"
			value=""
		>
			-- vyberte --
		</option>
		<option
			v-for="(item, key) in field_data.enum"
			:value="option_value(item)"
		>
			{{ option_label(item) }}
		</option>
	</select>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import combined_select_mixin from '@/components/forms/dynamic_input_mixins/combined_select_mixin.js';
	import auto_select_mixin from '@/components/forms/dynamic_input_mixins/auto_select_mixin.js';
	import vybava_select_mixin from '@/components/forms/dynamic_input_mixins/vybava_select_mixin.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		mixins: [combined_select_mixin, auto_select_mixin, vybava_select_mixin],
		data() {
			return {
				loading: false,
			}
		},
		created() {
		},
		methods: {
			// we have two types of enum options
			// one straight with key=>string (rokvyroby), other also with useless category data key=>object (auto_znacka)
			option_value(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return key;
				} else if (typeof value === 'object') {
					return value.value;
				}
			},
			option_label(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return value;
				} else if (typeof value === 'object') {
					return value.label;
				}
			},
		},
		computed: {
			is_dynamic_select()
			{
				return this.dynamic.hasOwnProperty(this.field_id) || this.field_id === this.vybava_id;
			},
			is_disabled()
			{
				if (this.loading === true) {
					return true;
				}
				return this.field_data.disabled;
			},
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
					if (this.emit_name !== undefined) {
						this.$emit(this.emit_name);
					}
					// vybava only, adds label into session
					if (this.field_id === this.vybava_id) {
						let label = '';
						for (let order in this.field_data.enum) { if (!this.field_data.enum.hasOwnProperty(order)) { continue; }
							let key = Object.keys(this.field_data.enum[order])[0];
							if (key === event_value) {
								label = this.field_data.enum[order][key];
							}
						}
						STATE_params().set_form_value(this.vybava_additional_label, label);
					}
				},
			},
		},
		watch: {
		},
	}
</script>
