<template>
	<div class="row mt-4 mb-2 justify-content-center" data-cy="pagination">
		<div v-for="(value, key) in all_form_tabs" class="col-1 text-center">
			<div v-if="count_tab_errors(key) > 0" class="error_counter_info" >
				<span class="mark-circle mark-orange">!</span>
				{{ count_tab_errors(key) }}
			</div>
			<button :class="['btn', 'btn-sm', (key == tab_current) ? 'btn-grey' : 'btn-white']" @click="switch_tab(key)" >
				tab {{ key }}
			</button>
		</div>
	</div>
	<div class="row">
		<div class="col-12">
			<Tabs :tab_current="tab_current" />
		</div>
	</div>
	<div class="row mt-5" data-cy="navigation">
		<div class="col text-center">
			<button v-if="show_back_button" class="btn btn-primary btn-lg btn-left" @click="prev()" >
				Nasp
			</button>
		</div>
		<div class="col text-center">
			<button class="btn btn-primary btn-lg btn-right" @click="next()" >
				Pokraova
			</button>
		</div>
	</div>
</template>

<script>
	import Get_form from '@/logic/flow_only/Get_form.js';
	import Tabs from '@/components/forms/Tabs.vue';
	import STATE_forms from '@/store/STATE_forms.js';

	export default {
		props: {
			action: Object,
		},
		components: {
			Tabs,
		},
		data() {
			return {
				tab_current: 0,
				tab_last: 0,
			}
		},
		mounted() {
			Get_form.osmapi_get_form(this.action).then(
				(response) => {
					if (response.status !== 200) {
						return;
					}
					var form = response.result.data.resultData.form;
					STATE_forms().set_form(form);
					this.tab_last = Object.keys(form).length;
					this.tab_current = parseInt( Object.keys(form)[0], 10 );
				}
			);
		},
		methods: {

			/**
			 * prev
			*/
			prev() {
				if (this.tab_current <= 1) {
					return;
				}
				this.tab_current--;
			},

			/**
			 * next
			*/
			next() {
				if (this.tab_current >= this.tab_last) {
					this.validate_form();
					return;
				}
				this.tab_current++;
			},

			/**
			 * switch_tab
			*/
			switch_tab(tab_number)
			{
				this.tab_current = parseInt(tab_number, 10);
			},

			/**
			 * validate_form
			*/
			validate_form()
			{
				Get_form.osmapi_validate_form(this.action).then(
					(response) => {
						STATE_forms().set_errors(response.result.data.resultData.form_validation_error_elements);
						// if (response.result.data.resultData.validform === false) {
							// this.show_first_tab_with_error();
						// }
						if (response.result.data.resultData.validform !== false) {
							STATE_forms().clear_form();
							this.$emit('next_step');
						}
					}
				);
			},

		},
		computed: {
			show_back_button() {
				return (this.tab_last > 1 && this.tab_current > 1) ? true : false;
			},
			all_form_tabs() {
				return STATE_forms().get_form();
			},
			// undocumented, but recommended by vue maintainers, way to use parameter in computed
			count_tab_errors() {
				return (errors_from_tab_number) => {
					var form = STATE_forms().get_form();
					var errors = STATE_forms().get_errors();
					var counter = 0;
					for ( var tab in form ) {
						if ( ! form.hasOwnProperty( tab ) ) { continue; }
						var inputs = form[tab];
						for ( var order in inputs ) {
							if ( ! inputs.hasOwnProperty( order ) ) { continue; }
							var input_id = Object.keys(inputs[order])[0];
							if (
								errors.hasOwnProperty(input_id)
								&&
								tab === errors_from_tab_number
							) {
								counter++;
							}
						}
					}
					return counter;
				}
			},
		},
	}
</script>

<style scoped lang="scss">
	.error_counter_info {
		position: relative;
		height: 0px;
		right: -37px;
		bottom: 5px;
		color: var(--accent) !important;
		span {
			margin-right: 0px !important;
		}
	}
</style>
