<template>
	<section>

		<Title
			:title="title"
			:subtitle="subtitle"
		/>

		<div v-if="success !== null" class="row gx-3 gy-3">

			<!-- 1 payment -->
			<Point
				v-if="!success"
				:order="1"
				title="Uhradi poistenie"
			>
				<div
					class="row"
				>
					<div class="col-md-4 mb-3 mb-md-0">
						<!-- payment gateways -->
						<Gateways />
					</div>
					<div class="col-sm-6 col-md-4 mb-3 mb-md-0">
						<!-- payment info -->
						<Info />
					</div>
					<div class="col-sm-6 col-md-4">
						<!-- payment qr code -->
						<Qr />
					</div>
				</div>
			</Point>

			<!-- 2 documents -->
			<Point
				:order="success ? 1 : 2"
				title="Uloi dokumenty"
			>
				<Documents_prepare
					:start_doc_requests="start_doc_requests"
					step_html="5"
				/>
			</Point>

			<!-- 3 next steps -->
			<Point
				:order="success ? 2 : 3"
				title="E-mail a alie intrukcie"
			>
				<p>
					Do 30 dn dostanete potou potvrdenie o uzatvoren zmluvy ako aj potrebn dokumenty (biela karta, zelen karta,...).
				</p>
				<p>
					Vetky tieto informcie a dokumenty sme Vm poslali aj e-mailom, aby ste sa k nim vedeli kedykovek dosta.
				</p>
				<p class="text-warning">
					<strong>
						Ak je potrebn ete nieo doplni pre poisovu, tak podrobn postup njdete rovnako v texte e-mailu.
					</strong>
				</p>
			</Point>

		</div>

	</section>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import Documents_prepare from '@/components/form_app/documents/Documents_prepare.vue';
	import Gateways from '@/components/form_app/payment/Gateways.vue';
	import Info from '@/components/form_app/payment/Info.vue';
	import Qr from '@/components/form_app/payment/Qr.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_params from '@/store/STATE_params.js';
	import Title from '@/components/form_app/payment/html_slots/Title.vue';
	import Point from '@/components/form_app/payment/html_slots/Point.vue';
	import Payment_requests from '@/logic/requests/Payment_requests.js';

	export default {
		props: {
		},
		components: {
			Documents_prepare,
			Gateways,
			Info,
			Qr,
			Title,
			Point,
			Payment_requests,
		},
		emits: [],
		data() {
			return {
				success: null,
				start_doc_requests: false,
			}
		},
		created() {
			// get_data for selected_product which is needed for documents
			Promise.resolve().then(
				() => {
					return Session_data.get_data();
				}
			).then(
				(response) => {
					STATE_params().extract_selected_product(response);
					// save receiver for remap
					this.receiver = parseInt(response.result?.call_params?.receiver, 10);
					// start document requests
					this.start_doc_requests = true;
				}
			);
			// check for payment success
			let payment = STATE_core().get_payment_data();
			if (!payment.paymentid || !payment.result) {
				console.log('ERROR: payment is empty', structuredClone(payment));
				return;
			}
			Payment_requests.payment_result(payment.paymentid, payment.result).then(
				(response) => {
					console.log(response.result);
					this.success = response.result === 'OK' ? true : false;
				}
			);
		},
		methods: {
		},
		computed: {
			title()
			{
				if (this.success === null) {
					return 'Spracovvam platobn informciu...';
				}
				return this.success ? 'Platba spen!' : 'Platba nespen!';
			},
			subtitle()
			{
				if (this.success === null) {
					return '';
				}
				return this.success ? 'Gratulujeme! Vaa zmluva je platn.' : 'Platba nebola spen alebo informcia a zrealizovan platby je nedostupn. Prosm skontrolujte si vsledok cez internetbanking.';
			},
		},
		watch: {
		},
	}
</script>
