<template>
	<div class="row mt-4 form-pagination" data-cy="navigation">
		<div class="col-12 col-sm-4 text-center text-sm-start order-2 order-sm-1">
			<button
				v-if="show_back"
				@click="prev"
				type="button"
				class="btn btn-light btn-left btn-lg"
			>
				Nasp
			</button>
		</div>
		<div class="col-12 col-sm-4 text-center order-3 order-sm-2">
			<button
				@click="clear"
				type="button"
				class="btn btn-white btn-lg"
			>
				Vymaza daje
			</button>
		</div>
		<div class="col-12 col-sm-4 text-center text-sm-end order-1 order-sm-3">
			<Bouncer_loading
				v-show="is_loading"
				style="margin-top: 18px; margin-right: 50px;"
			/>
			<button
				v-show="!is_loading"
				@click="next"
				:disabled="disable_next"
				:title="disable_next ? 'Pokame sa zopakova poiadavku.' : ''"
				type="button"
				class="btn btn-primary btn-right btn-lg"
			>
				Pokraova
			</button>
		</div>
	</div>
</template>

<script>
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			show_back: Boolean,
			disable_next: Boolean,
		},
		components: {
			Bouncer_loading,
		},
		emits: ['next', 'prev'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			next() {
				this.$emit('next');
			},
			prev() {
				this.$emit('prev');
			},
			clear() {
				Functions.clear_session();
			},
		},
		computed: {
			is_loading()
			{
				return STATE_modals().which_is_open() === 'loading';
			},
		},
	}
</script>
