<template>
	<section>

		<Tab_pagination :tab_current="tab_current" :tab_last="tab_last" />

		<Tabs :tab_current="tab_current" />

		<Prev_next_buttons @prev="prev_button" @next="next_button" :show_back="show_back" :disable_next="false" />

	</section>
</template>

<script>
	import Prev_next_buttons from '@/components/form_app/nav/Prev_next_buttons.vue';
	import Group_user_form from '@/logic/requests/Group_user_form.js';
	import Functions from '@/logic/Functions.js';
	import Tabs from '@/components/forms/Tabs.vue';
	import STATE_forms from '@/store/STATE_forms.js';
	import STATE_core from '@/store/STATE_core.js';
	import Tab_pagination from '@/components/form_app/nav/Tab_pagination.vue';
	import Session_data from '@/logic/requests/Session_data.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
		},
		components: {
			Prev_next_buttons,
			Tabs,
			Tab_pagination,
		},
		data() {
			return {
				is_step: 1,
				tab_first: 0,
				tab_current: 0,
				tab_last: 0,
			}
		},
		created() {
			// loading
			STATE_modals().open_it('loading');
			// 1 or 3
			this.is_step = STATE_core().current_step();
			// step3 first asks for selected_product if does not follow step2
			Promise.resolve().then(
				() => {
					if (this.is_step === 3 && STATE_params().get_selected_product().form_id === undefined) {
						return Session_data.get_data().then(
							(response) => {
								STATE_params().extract_selected_product(response);
							}
						);
					} else {
						return Promise.resolve();
					}
				}
			).then(
				() => {
					// step1, or step3 with selected_product
					Group_user_form.getform().then(
						(response) => {
							// close loading, but dont close 423 modal
							if (response.status !== 423) {
								STATE_modals().close_them_all();
							}
							// error
							if (response.status !== 200) {
								return;
							}
							// save session token if fresh
							STATE_core().set_session_token(response.result.token, false);
							// save call_params
							STATE_params().store_call_params(response.result.call_params);
							// save form
							let form = response.result.form;
							STATE_forms().set_form(form);
							STATE_params().insert_form_values(form);
							// set tabs
							this.set_tabs(form);
						}
					);
				}
			);
		},
		methods: {
			prev_button()
			{
				if (this.tab_current === this.tab_first) {
					STATE_forms().clear_form();
					STATE_params().reset_form_values();
					STATE_core().change_step(2);
				} else {
					this.tab_current--;
					if (this.tab_is_empty()) {
						this.prev_button();
					}
				}
			},
			next_button()
			{
				// loading
				STATE_modals().open_it('loading');
				Group_user_form.validateform().then(
					(response) => {
						// close loading, but dont close 423 modal
						if (response.status !== 423) {
							STATE_modals().close_them_all();
						}
						// error
						if (response.status !== 200) {
							return;
						}
						// reset errors
						STATE_forms().clear_errors();
						this.check_errors_go_next(response);
					}
				);
			},
			check_errors_go_next(response)
			{
				// last tab validates whole step
				if (this.tab_current === this.tab_last) {
					// valid go next step
					if (response.result.validform === true) {
						STATE_forms().clear_form();
						STATE_params().reset_form_values();
						STATE_core().change_step(this.is_step + 1);
					}
					// invalid show first tab with error
					else {
						STATE_forms().set_errors(response.result.form_validation_error_elements);
						let first_tab = Object.keys(response.result.form_validation_error_tab_elements)[0];
						this.tab_current = parseInt(first_tab, 10);
					}
				}
				// tabs before validates only themself
				else {
					// valid go next tab
					if (response.result.validform === true || response.result.form_validation_error_tab_elements[this.tab_current] === undefined) {
						this.tab_current++;
						if (this.tab_is_empty()) {
							this.check_errors_go_next(response);
						}
					}
					// invalid show errors for this tab
					else {
						STATE_forms().set_errors(response.result.form_validation_error_tab_elements[this.tab_current]);
					}
				}
			},
			set_tabs(form)
			{
				let tab_count = Object.keys(form).length;
				this.tab_last = Object.keys(form)[ tab_count - 1 ];
				this.tab_last = parseInt(this.tab_last, 10);
				this.tab_first = Object.keys(form)[0];
				this.tab_first = parseInt(this.tab_first, 10);
				this.tab_current = this.tab_first;
			},
			tab_is_empty()
			{
				// check if it exists (can skip order), or has shown inputs (with show_hide)
				let active_tab = Functions.active_tab(this.tab_current);
				return (active_tab === undefined/*missing tab*/ || Object.keys(active_tab).length === 0/*all hidden*/);
			},
		},
		computed: {
			show_back()
			{
				return (this.is_step === 3 || this.tab_current > this.tab_first) ? true : false;
			},
		},
	}
</script>
