<template>
	<div :class="[size]" :data-cy="cypres">
		<Image_action v-if="type === 'image'" :action="action" />
		<Navigation_action v-else-if="type === 'navigation'" :action="action" @requested_step="requested_step" />
		<Form_action v-else-if="type === 'getForm'" :action="action" @next_step="next_step" />
		<Html_action v-else-if="type === 'getHtml'" :action="action" />
	</div>
</template>

<script>
	import Image_action from '@/components/flow/actions/Image_action.vue';
	import Navigation_action from '@/components/flow/actions/Navigation_action.vue';
	import Form_action from '@/components/flow/actions/Form_action.vue';
	import Html_action from '@/components/flow/actions/Html_action.vue';

	export default {
		props: {
			action: Object,
		},
		components: {
			Image_action,
			Navigation_action,
			Form_action,
			Html_action,
		},
		data() {
			return {
			}
		},
		mounted() {
		},
		emits: ['requested_step', 'next_step'],
		methods: {
			requested_step(int) {
				this.$emit('requested_step', int);
			},
			next_step() {
				this.$emit('requested_step');
			},
		},
		computed: {
			type() {
				return this.action.function;
			},
			size() {
				if (this.action.functionPosition.size === 'full') {
					return 'col-12';
				} else if (this.action.functionPosition.size === 'half') {
					return 'col-12 col-lg-6';
				} else {
					return 'col-12';
				}
			},
			cypres() {
				return 'action_' + this.action.function + '_order_' + this.action.functionPosition.order;
			},
		},
	}
</script>
