import { createApp } from 'vue'
import App from './App.vue'
import { createPinia } from 'pinia'
const pinia = createPinia()

import STATE_core from './store/STATE_core.js'
import STATE_docs_landing from './store/STATE_docs_landing.js'
import STATE_error from './store/STATE_error.js'
import STATE_modals from './store/STATE_modals.js'
import STATE_params from './store/STATE_params.js'
import STATE_forms from './store/STATE_forms.js'

class Osm_app
{
	app = {};

	/**
	 * init
	*/
	init(config)
	{
		this.app = createApp(App).use(pinia);
		this.app.provide('config', config).mount('#osm_app');
	}

	/**
	 * destroy
	*/
	destroy()
	{
		STATE_core().$reset();
		STATE_docs_landing().$reset();
		STATE_error().$reset();
		STATE_modals().$reset();
		STATE_params().$reset();
		STATE_forms().$reset();
		this.app.unmount();
		this.app = '';
	}

	/**
	 * dump
	*/
	dump()
	{
		return structuredClone(pinia.state.value);
	}

	/**
	 * insert_new_bearer
	*/
	insert_access_token(string)
	{
		// NOT IN USE ANYMORE, DELETE AFTER WHILE, 14.02.2023
		STATE_core().save_bearer(string);
	}

	/**
	 * autofill
	*/
	autofill_run()
	{
		STATE_params().autofill_run();
	}
}

window.osm_app = new Osm_app;
