<template>
	<textarea
		:id="field_id"
		:name="field_id"
		v-model="value"
		:placeholder="field_data.placeholder"
		:rows="area_rows"
		class="form-control"
	></textarea>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			area_rows()
			{
				let rows = this.field_data.wrapper_attrs.match(/rows="(.*?)"/)?.[0].replace('rows="', '').replace('"', '');
				return rows === undefined ? 3 : rows;
			},
			value:
			{
				get() {
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
				},
			},
		},
		watch: {
		},
	}
</script>
