<template>
	<div class="flatpickr_scope input-datepicker">
		<input
			:id="field_id"
			:name="field_id"
			v-model="value"
			ref="input"
			type="text"
		>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import flatpickr from 'flatpickr';
	import { Slovak } from 'flatpickr/dist/l10n/sk.js';
	import date_ranges_mixin from '@/components/forms/dynamic_input_mixins/date_ranges_mixin.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		mixins: [date_ranges_mixin],
		data() {
			return {
			}
		},
		created() {
		},
		mounted() {
			this.init_date_library();
		},
		methods: {
			init_date_library()
			{
				let options = {
					static: true,
					dateFormat: 'd.m.Y',
					defaultDate: this.value,
					allowInput: true,
					disableMobile: 'true',
					locale: Slovak,
					minDate: '',
				};
				// inputs in ranges.from should not have value in past
				let i, len = this.ranges.length;
				for (i = 0; i < len; i++) {
					if (this.ranges[i].from === this.field_id) {
						options.minDate = 'today';
					}
				}
				// inputs in ranges.till should not have value before ranges.from
				if (this.parent_value !== null) {
					options.minDate = this.parent_value;
				}
				flatpickr(this.$refs.input, options);
			},
		},
		computed: {
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					return STATE_params().get_form_value(this.field_id);
				},
				set() {
					let event_value = this.$refs.input.value; // flatpickr does not trigger value change
					STATE_params().set_form_value(this.field_id, event_value);
					if (this.emit_name !== undefined) {
						this.$emit(this.emit_name);
					}
				},
			},
		},
		watch: {
		},
	}
</script>

<style scoped lang="scss">
	.flatpickr_scope::v-deep() {
		@import '@/assets/libraries/flatpickr_4.6.9/flatpickr.scss';
	}
</style>
