<template>
	<div class="row" data-cy="input_tab">
		<Wrap v-for="(field, order) in compute_active_tab" :field_data="field_data(field)" :field_id="field_id(field)" />
	</div>
</template>

<script>
	import Wrap from '@/components/forms/Wrap.vue';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			tab_current: Number,
		},
		components: {
			Wrap,
		},
		data() {
			return {
			}
		},
		methods: {
			field_id(field) {
				return Object.keys(field)[0];
			},
			field_data(field) {
				return field[ Object.keys(field)[0] ];
			},
		},
		computed: {
			compute_active_tab() {
				return Functions.active_tab(this.tab_current);
			},
		},
	}
</script>
