<template>
	<div class="text-center pt-5">
		<h1>
			utujeme, nieo sa nepodarilo :(
		</h1>
		<p>
			Vyskytla sa neoakvan chyba, preto nebolo mon dokoni operciu.<br>
			Pre informcie ohadom alieho postupu ns prosm kontaktujte.
		</p>
		<div v-if="show_phone" >
			<h3 class="text-primary">
				{{ phone }}
			</h3>
			<p>
				Volajte kedykovek pondelok a piatok 09:00 - 17:00
			</p>
		</div>
		<h3 class="text-primary">
			{{ email }}
		</h3>
		<p>
			Polite nm fotku tejto obrazovky a ozveme sa vm.
		</p>
	</div>

	<pre>{{ error }}</pre>

	<Prev_next_buttons @prev="prev_button" @next="next_button" show_back="true" :disable_next="disable_next" />

	<button
		@click="open_where"
		class="btn btn-secondary mt-5"
	>
		? Kame patr tto session ?
	</button>
	<p>
		<small>
		toto je experimentalna feature, moze pomoct debugnut session, postupne automaticky validuje stepy aby urcila ktory bol posledny uspesny krok
		</small>
	</p>
</template>

<script>
	import Prev_next_buttons from '@/components/form_app/nav/Prev_next_buttons.vue';
	import STATE_error from '@/store/STATE_error.js';
	import Constants from '@/logic/Constants.js';
	import STATE_params from '@/store/STATE_params.js';
	import STATE_core from '@/store/STATE_core.js';
	import Session_data from '@/logic/requests/Session_data.js';
	import Confirm_requests from '@/logic/requests/Confirm_requests.js';
	import STATE_modals from '@/store/STATE_modals.js';

	export default {
		props: {
		},
		components: {
			Prev_next_buttons,
		},
		emits: [],
		data() {
			return {
				disable_next: true,
				attempt: 0,
				max_tries: 20,
				session_state: 0,
				timeout_handle: '',
			}
		},
		created() {
			this.continue_session();
		},
		methods: {
			prev_button()
			{
				clearTimeout(this.timeout_handle);
				STATE_core().change_step(this.error.step);
			},
			next_button()
			{
				// if step4 confirm finished on background and changed state, allow to go forward to step5
				if (this.session_state === 100) {
					STATE_core().change_step(5);
					return;
				}
				// else session_state === 90
				// almost confirmed, all requests are forbiden, but confirm has to be repeated
				Confirm_requests.repeat_confirm().then(
					(response) => {
						if (response.result?.response === 'success'/*confirm*/ || response.result?.state === 100/*getsessioninfo*/) {
							STATE_core().change_step(5);
						}
					}
				);
			},
			continue_session()
			{
				Session_data.session_info().then(
					(response) => {

						if (response.result?.response === 'success'/*confirm*/ || response.result?.state === 100/*getsessioninfo*/) {
							// state 100 means the session was confirmed by third party
							this.session_state = 100;
							this.disable_next = false;
						} else if (response.result?.state === 90) {
							this.session_state = 90;
							this.disable_next = false;
						} else {
							if (this.attempt > this.max_tries) {
								// repeat in 5 seconds unless max_tries are reached
								console.log('continue_session reached max_tries');
								return;
							}
							this.attempt++;
							this.timeout_handle = setTimeout(
								() => { this.continue_session() },
								5000 // 5s
							);
						}
					}
				);
			},
			open_where()
			{
				clearTimeout(this.timeout_handle);
				STATE_modals().open_it('where_to_session');
			},
		},
		computed: {
			error()
			{
				return STATE_error().get_last_request();
			},
			email()
			{
				return Constants.email_address();
			},
			show_phone()
			{
				return ! STATE_params().direct_user_no_agent();
			},
			phone()
			{
				return Constants.phone_number(true);
			},
		},
		watch: {
		},
	}
</script>
