<template>
	<div
		:class="columns"
		:data-hide="hide"
		:data-cy="'input_wrap-' + field_id"
	>
		<!-- errors -->
		<div class="validation-placeholder validation-message">
			<span
				v-for="(value, key) in has_error"
			>
				<span class="mark-circle mark-orange">!</span>
				{{ value }}
			</span>
		</div>
		<div
			:class="[required, blank, label_size, error_class]"
			class="input-block d-flex align-items-start align-items-sm-center flex-column"
		>
			<!-- label -->
			<div
				v-if="label"
				class="input-label"
			>
				{{ label }}
			</div>
			<div
				:class="input_block_class"
				class="input-area"
				:data-cy="'input-' + field_id"
			>
				<!-- input -->
				<Input_switcher
					v-for="(value, key) in inputs"
					:key="key"
					:field_data="value"
					:field_id="key"
				/>
			</div>
			<!-- tooltip -->
			<div
				v-if="tooltip"
				class="ttip"
			>
				?
				<span class="ttip_text">
					{{ tooltip }}
				</span>
			</div>
		</div>
	</div>
</template>

<script>
	import Input_switcher from '@/components/forms/Input_switcher.vue';
	import Show_hide from '@/logic/Show_hide.js';
	import STATE_forms from '@/store/STATE_forms.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
			Input_switcher,
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			inputs()
			{
				let inputs = {};
				// separate single inputs from block inputs
				if (this.field_data.elements === undefined) {
					// normal way where every field contain single input
					inputs[this.field_id] = this.field_data;
				} else {
					// some fields are blocks of multiple inputs
					for (let key in this.field_data.elements) { if (!this.field_data.elements.hasOwnProperty(key)) { continue; }
						// Show_hide inside block
						let input = { [key]: this.field_data.elements[key] };
						if (Show_hide.visibility_boolean(input)) {
							inputs[key] = this.field_data.elements[key];
						}
					}
				}
				return inputs;
			},
			has_error()
			{
				let errors = STATE_forms().get_errors();
				let found = [];
				for (let id in this.inputs) { if (!this.inputs.hasOwnProperty(id)) { continue; }
					if (!errors.hasOwnProperty(id)) {
						continue;
					}
					let error = '';
					if (typeof errors[id] === 'object') {
						error = Functions.first_item(errors[id]);
					} else {
						error = errors[id][0];
					}
					found.push(error);
				}
				return found;
			},
			error_class()
			{
				if (this.has_error.length === 0) {
					return '';
				} else {
					return 'validation-error';
				}
			},
			columns()
			{
				let size = '';
				if (this.field_data.block_settings !== undefined && this.field_data.block_settings !== null) {
					size = this.field_data.block_settings.size;
				} else if (this.field_data.type_settings !== undefined && this.field_data.type_settings !== null) {
					size = this.field_data.type_settings.size;
				} else {
					return 'col-12';
				}
				if (size === 'full') {
					return 'col-12';
				} else if (size === 'half') {
					return 'col-lg-6';
				} else if (size === 'third') {
					return 'col-lg-4';
				} else {
					return 'col-12';
				}
			},
			required()
			{
				return '';
				// required is temporarily turned off on client's request
				// return 'input-required';
			},
			blank()
			{
				if (this.field_data.type_settings?.design === 'blank') {
					return 'input-block-blank';
				} else {
					return '';
				}
			},
			hide()
			{
				if (this.field_data.elements === undefined) {
					return this.field_data['data-hide-condition'];
				} else {
					return this.field_data['block_hide_condition'];
				}
			},
			label_size()
			{
				if (this.field_data.type_settings?.label_size === 'fullwidth') {
					return 'input-label-full';
				} else {
					return 'flex-sm-row';
				}
			},
			label()
			{
				if (this.field_data.type === 'checkbox') {
					// simple checkboxes doesnt have label
					return false;
				} else if (this.field_data.block_settings?.block_layout === 'checkbox_collapse') {
					// block checkbox_collapse doesnt have label
					return false;
				} else {
					// every other input has label
					if (this.field_data.elements === undefined) {
						// simple fields uses own label
						return this.field_data.label;
					} else {
						// blocks uses block label
						return this.field_data.block_label;
					}
				}
			},
			input_block_class()
			{
				if (this.field_data.elements === undefined) {
					// normal way where every field contain single input
					return '';
				} else {
					// some fields are blocks of multiple inputs
					// .input-mixed determine the input is block type, few block layouts adds additional class
					if (this.field_data.block_settings.block_layout === 'select_text') {
						return 'input-mixed input-select-text';
					} else if (this.field_data.block_settings.block_layout === 'checkbox_collapse') {
						return 'input-mixed input-onoff';
					} else {
						return 'input-mixed';
					}
				}
			},
			tooltip()
			{
				// replacing line break so they could easily use enters in editors
				// replacing &quot; because the couldnt use double quotes in old system
				if (this.field_data.description !== undefined && this.field_data.description !== '') {
					return this.field_data.description.replace(/\r\n/g, '<br>').replace(/&quot;/g, '"');
				} else if (this.field_data.block_input_hint !== undefined && this.field_data.block_input_hint !== '') {
					return this.field_data.block_input_hint.replace(/\r\n/g, '<br>').replace(/&quot;/g, '"');
				} else {
					return false;
				}
			},
		},
		watch: {
		},
	}

	/*
	allowed settings
	{
		"size": "full",
		"size": "half",
		"size": "third",
		"select_type": "simple",
		"select_type": "searchable",
		"radio_layout": "horizontal",
		"radio_layout": "vertical",
		"radio_layout": "icons",
		"radio_layout": "blank",
		"plus_minus": "true",
		"plus_minus_min": 0,
		"plus_minus_max": 9,
		"design": "blank",
		"disabled": "true",
		"calendar_type": "standard",
		"calendar_type": "years_first",
		"calendar_type": "no_value_show_oldies",
		"block_layout": "select_text",
		"block_layout": "checkbox_collapse",
		"checkbox_icon": "true", // not used anymore as class, moved to wrapper_attrs as pure string without markup
		"label_size": "fullwidth",
		"upload": "scan_modal",
		"transform": "uppercase",
		"transform": "capitalize",
	}
	"checkbox_icon" options:
	zp_smrt
	zp_trvale_nasledky
	zp_invalidita
	zp_denne_odskodne
	zp_kriticke_choroby
	zp_praceneschopnost
	*/

</script>
