import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Send_email
*/
class Send_email
{
	/**
	 * email_save
	*/
	email_save(payload)
	{
		let requestParams = {
			action_name: 'email',
			method: 'POST',
			body: payload,
			url: STATE_core().endpoint_url() + 'online/email/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * offer_notify
	*/
	offer_notify(subject)
	{
		let requestParams = {
			action_name: 'offer-notify',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/offer-notify/?' + Functions.add_token_as_parameter(),
		};
		subject = (subject === '') ? ' poistenie' : subject;
		requestParams.url += '&email_subject=' + encodeURIComponent(subject);

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Send_email;
