import STATE_params from '@/store/STATE_params.js';

/**
 * Show_hide
*/
class Show_hide
{
	id = '';
	hide_received = '';
	hide_parsed = '';
	field_names = [];
	hide_replaced = '';
	hide_evaluated = '';

	/**
	 * visibility_boolean
	*/
	visibility_boolean(/*obj*/ input)
	{
		// constructor
		this.prepare_input(input);

		// always show without hide-condition
		if (this.hide_received === '') {
			return true;
		}

		// actions here
		this.parse_condition();
		this.get_field_names();
		this.replace_conditions_with_input_values();
		this.evaluate_condition();
		this.purge_value();

		// visible if not hidden
		return ! this.hide_evaluated;
	}

	/**
	 * prepare_input
	*/
	prepare_input(input)
	{
		this.id = Object.keys(input)[0];
		this.hide_received = input[this.id]['data-hide-condition'];
		this.type = input[this.id]['type'];
		// blocks
		if (this.id === '_block') {
			this.hide_received = input[this.id]['block_hide_condition'];
		}
	}

	/**
	 * parse_condition
	*/
	parse_condition()
	{
		// parse backoffice format into JS comparable conditions
		var string = this.hide_received;

		var space	= new RegExp('[ ]', 'g');
		var eq		= new RegExp('[=][=]', 'g');
		var lt		= new RegExp('[&][l][t][;]', 'g');
		var gt		= new RegExp('[&][g][t][;]', 'g');
		var less	= new RegExp('[<]', 'g');
		var more	= new RegExp('[>]', 'g');
		var n		= new RegExp('[!]', 'g');
		var plus	= new RegExp('\\+', 'g');
		var minus	= new RegExp('\\-', 'g');
		var les_eq	= new RegExp('< =', 'g');
		var mor_eq	= new RegExp('> =', 'g');
		var not_eq	= new RegExp('!=', 'g');
		var qot		= new RegExp('&quot;&quot;', 'g');
		var amp		= new RegExp('[&][a][m][p][;]', 'g');
		var and		= new RegExp('[&]', 'g');
		var or		= new RegExp('[|]', 'g');

		string = string.replace(space, '');

		string = string.replace(eq, ' == '); // pridaj medzeru kvli parsovaniu neskr
		string = string.replace(lt, '<'); // nahrad kod znamienkom a pridaj medzeru
		string = string.replace(gt, '>'); // nahrad kod znamienkom a pridaj medzeru

		string = string.replace(less, ' < '); // nahrad kod znamienkom a pridaj medzeru
		string = string.replace(more, ' > '); // nahrad kod znamienkom a pridaj medzeru
		string = string.replace(n, ' !'); // pridaj medzeru

		string = string.replace(plus, ' + ');
		string = string.replace(minus, ' - ');
		string = string.replace(les_eq, '<= ');
		string = string.replace(mor_eq, '>= ');
		string = string.replace(not_eq, '!= ');

		string = string.replace(qot, '-1'); // oprav uvodzovky

		string = string.replace(amp, '&');
		string = string.replace(and, ' && '); // oprav AND
		string = string.replace(or, ' || '); // oprav OR

		this.hide_parsed = string;
	}

	/**
	 * get_field_names
	*/
	get_field_names()
	{
		// extract field IDs/names from condition string
		var string = this.hide_parsed;
		var names_array = [];

		// if condition has brackets, explode it by them as well
		string = string.split(')').join('&');
		string = string.split('(').join('&');
		var splitted = string.split(/[\s&|()=<>!]+/);

		// remove empty strings from array
		var i, len = splitted.length;
		for (i = len-1; i >= 0; i--) {
			if (splitted[i] === '') {
				splitted.splice(i, 1);
			}
		}

		// check for dolar in condition
		if (string.indexOf('$') > -1) {

			// if string is having dolar, get all strings starting with dolar
			var i, len = splitted.length;
			for (i = 0; i < len; i++) {
				if (splitted[i].indexOf('$') === 0) {
					names_array.push(splitted[i]);
				}
			}

		} else {

			// if not, every odd (not even) item in array is the name of field
			var i, len = splitted.length;
			for (i = 0; i < len; i++) {
				if (i % 2 === 0) {
					names_array.push(splitted[i]);
				}
			}

		}

		// contains duplicates
		this.field_names = names_array;
	}

	/**
	 * replace_conditions_with_input_values
	*/
	replace_conditions_with_input_values()
	{
		var string = this.hide_parsed;

		var i, len = this.field_names.length;
		for (i = 0; i < len; i++) {

			var id = this.field_names[i];
			var value = STATE_params().get_user_input(id.replace('$', ''));
			if (value === undefined || value === '') {
				value = 0;
			}
			string = string.replace(id, value);

		}

		this.hide_replaced = string;
	}

	/**
	 * evaluate_condition
	*/
	evaluate_condition()
	{
		var condotion_string = this.hide_replaced;

		try {

			// accepts string, returns boolean
			this.hide_evaluated = eval(condotion_string);

		} catch (error) {

			// report errors
			if (error instanceof SyntaxError) {
				console.log('syntax error');
				console.log(error.message);
				console.log(this.hide_received);
				console.log(this.hide_replaced);
				this.hide_evaluated = false;
			} else {
				console.log('probably parsing error, check for missing $');
				console.log(error);
				console.log(this.hide_received);
				console.log(this.hide_replaced);
				this.hide_evaluated = false;
			}

		}
	}

	/**
	 * purge_value
	*/
	purge_value()
	{
		// leave shown inputs
		if (this.hide_evaluated === false) {
			return;
		}
		// radios and checkboxes keep value when hidden
		if (this.type === 'radio' || this.type === 'checkbox' || this.type === 'checkbox_icon' || this.type === 'select') {
			return;
		}
		// remove value if hiding input
		STATE_params().delete_form_value(this.id);
	}

}

export default new Show_hide;
