// import State from '@/store/State.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Flow_handling
*/
class Flow_handling
{
	/**
	 * flow_init_session
	*/
	flow_init_session()
	{
		var flow_id = 200; // hardcoded branos id in nodered

		let requestParams = {
			action_name: 'init',
			method: 'GET',
			url: State.endpoint_url() + 'osmapp/init/' + flow_id,
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {

				State.set_session_token(response.result.data.session);
				State.set_workflow_status(response.result.data.workflowStatus);

			}
		).catch(
			(error) => { throw error; }
		);
	}

	/**
	 * flow_next_step
	*/
	flow_next_step(requested)
	{
		var delay = 0; // seconds, can be used for debug

		let requestParams = {
			action_name: 'nextstep',
			method: 'GET',
			url: State.endpoint_url() + 'osmapp/nextstep/' + State.get_session_token(),
		};

		var array = [];
		if (requested !== undefined) {
			array.push('step=' + requested);
		}
		if (delay > 0) {
			array.push('&delay=' + (delay * 1000));
		}
		array.push('with-session-data=1');
		if (State.endpoint_type() !== 'prod') {
			array.push('debug-info=1');
		}
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {

				if (response.status === 422) {
					return;
				}
				State.set_workflow_status(response.result.data.workflowStatus);

			}
		).catch(
			(error) => { throw error; }
		);
	}

	/**
	 * flow_status
	*/
	flow_status()
	{
		let requestParams = {
			action_name: 'status',
			method: 'GET',
			url: State.endpoint_url() + 'osmapp/status/' + State.get_session_token(),
		};

		var array = [];
		array.push('with-session-data=1');
		if (State.endpoint_type() !== 'prod') {
			array.push('debug-info=1');
		}
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {

				State.set_workflow_status(response.result.data.workflowStatus);

			}
		).catch(
			(error) => { throw error; }
		);
	}
}

export default new Flow_handling;
