import STATE_forms from '@/store/STATE_forms.js';
import STATE_core from '@/store/STATE_core.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Reference_table
*/
class Reference_table
{
	/**
	 * auto_model_table
	*/
	auto_model_table(id, value)
	{
		let form = STATE_forms().get_form();
		let only_labels = {};
		for (let tab in form) { if (!form.hasOwnProperty(tab)) { continue; }
			for (let order in form[tab]) { if (!form[tab].hasOwnProperty(order)) { continue; }
				let field_id = Object.keys(form[tab][order])[0];
				if (field_id !== id) {
					continue;
				}
				let enums = form[tab][order][field_id].enum;
				for (let enum_order in enums) { if (!enums.hasOwnProperty(enum_order)) { continue; }
					let key = Object.keys(enums[enum_order])[0];
					let label = enums[enum_order][key];
					if (typeof label === 'object') {
						label = label.label;
					}
					only_labels[key] = label;
				}
			}
		}

		let requestParams = {
			action_name: 'reference_table_get',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/reference_table_get/?table_token=c_test_auto_model&ematch=' + only_labels[value],
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * psc_table
	*/
	psc_table(filter)
	{
		let requestParams = {
			action_name: 'reference_table_get',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/reference_table_get/?table_token=c_test_psc&filter=' + filter,
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Reference_table;
