import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';
import STATE_params from '@/store/STATE_params.js';

/**
 * Product_requests
*/
class Product_requests
{
	/**
	 * get_product_list
	*/
	async get_product_list()
	{
		let requestParams = {
			action_name: 'getproducts',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getproducts/' + STATE_core().product_group_id() + '/?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * select_product
	*/
	select_product()
	{
		let requestParams = {
			action_name: 'productselect',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/productselect/' + STATE_params().get_selected_product().product_id + '/?' + Functions.add_token_as_parameter(),
		};
		// toto nie je nevyhnutne kvoli requesty, ale kvoli mojim udajom v neskorsich stepoch
		requestParams.url += '&' + Functions.serialize_object(STATE_params().get_selected_product());

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Product_requests;
