<template>
	<Modal_body :close="true" title="Posla ponuku e-mailom?" >

		<div v-if="!success">
			<p>
				na V e-mail Vm poleme odkaz, cez ktor sa mete kedykovek vrti nasp a vetky vyplnen daje budete ma uloen u ns
			</p>

			<form @submit="email_submited">
				<input name="email" class="form-control mb-1" type="email" placeholder="Vaa e-mailov adresa" v-model="email">
				<input name="subject" id="email_subject" class="form-control my-1" type="text" placeholder="Nzov ponuky" v-model="subject">
				<button class="btn btn-primary w-100" type="submit" :disabled="loading">Odosla</button>
				<p v-show="error">Emailov adresa je povinn.</p>
				<Bouncer_loading v-show="loading" size="large" />
			</form>
		</div>

		<div v-else>
			<p>
				<span class="i_success"></span>
				<strong>Ponuka bola odoslan na V email!</strong>
			</p>
		</div>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Functions from '@/logic/Functions.js';
	import Send_email from '@/logic/requests/Send_email.js';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';

	export default {
		props: {
		},
		components: {
			Modal_body,
			Bouncer_loading,
		},
		data() {
			return {
				email: '',
				subject: '',
				error: false,
				loading: false,
				success: false,
			}
		},
		created() {
		},
		methods: {
			email_submited(event)
			{
				event.preventDefault();
				if (this.email === '') {
					this.error = true;
					setTimeout(
						() => { this.error = false },
						2500
					);
					return;
				}
				this.loading = true;
				let payload = Functions.custom_serialize_form_elements(event);
				Send_email.email_save(payload).then(
					() => {
						return Send_email.offer_notify(this.subject);
					}
				).then(
					() => {
						this.loading = false;
						this.success = true;
					}
				);
			},
		},
		computed: {
		},
	}
</script>
