import Request_service from '@/logic/Request_service.js';
// import State from '@/store/State.js';
import Functions from '@/logic/Functions.js';

/**
 * Get_form
*/
class Get_form
{
	/**
	 * osmapi_get_form
	*/
	osmapi_get_form(action)
	{
		let requestParams = {
			action_name: 'get-form',
			method: 'GET',
			url: State.endpoint_url() + 'osmapi/get-form/' + State.get_session_token(),
		};

		var array = [];
		array.push('source=' + action.functionParams.source); // group, forms
		array.push('source-id=' + action.functionParams['source-id']);
		array.push('action-id=' + action.id);
		// array.push('global=0');
		array.push('json=1');
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}

	/**
	 * osmapi_validate_form
	*/
	osmapi_validate_form(action)
	{
		// DEBUG HELPER
		// State.save_user_input('auto_model', 11);
		// HAS TO BE REMOVED IN FUTURE

		let requestParams = {
			action_name: 'validate-form',
			method: 'POST',
			url: State.endpoint_url() + 'osmapi/validate-form/' + State.get_session_token(),
			// body: JSON.stringify( Special_input_behaviour().get_all_user_inputs() ), // i bet this should not be .stringify() but serielize() -------- method renamed and data moved to form_values
			content_type: 'application/json',
		};
		console.log(requestParams, 'body stringify not serialize???');

		var array = [];
		array.push('source=' + action.functionParams.source); // group, forms
		array.push('source-id=' + action.functionParams['source-id']);
		array.push('action-id=' + action.id);
		// array.push('global=0');
		array.push('json=1');
		array.push('without_form=1');
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Get_form;
