import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Session_data
*/
class Session_data
{
	/**
	 * session_info
	*/
	session_info()
	{
		let requestParams = {
			action_name: 'getsessioninfo',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getsessioninfo/' + '?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}


	/**
	 * get_data
	*/
	get_data()
	{
		let requestParams = {
			action_name: 'getdata',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'online/getdata/' + '?' + Functions.add_token_as_parameter(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch( (error) => { throw error; } );
	}
}

export default new Session_data;
