<template>
	<div
		v-for="(item, order) in this.field_data.enum"
		:class="['form-check', radio_layout]"
	>
		<input
			:name="field_id"
			:id="radio_id(order)"
			v-model="value"
			:value="order"
			:disabled="field_data.disabled"
			type="radio"
			class="form-check-input"
		>
		<label
			:for="radio_id(order)"
			class="form-check-label"
		>
			{{ label(item) }}
		</label>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import combined_radio_mixin from '@/components/forms/dynamic_input_mixins/combined_radio_mixin.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		mixins: [combined_radio_mixin],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			radio_id(order)
			{
				return this.field_id + '-' + order;
			},
			label(item)
			{
				for (var key in item) {
					if (! item.hasOwnProperty(key)) { continue; }
					return item[key];
				}
			},
		},
		computed: {
			radio_layout()
			{
				if (
					this.field_data.type_settings === null ||
					this.field_data.type_settings === undefined ||
					this.field_data.type_settings.radio_layout === 'horizontal'
				) {
					return 'form-check-inline';
				} else if (this.field_data.type_settings.radio_layout === 'icons') {
					return 'input-button-icon';
				} else if (this.field_data.type_settings.radio_layout === 'vertical') {
					return '';
				} else if (this.field_data.type_settings.radio_layout === 'blank') {
					return 'input-button-blank';
				} else {
					return 'form-check-inline';
				}
			},
			value:
			{
				get() {
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
				},
			},
		},
		watch: {
		},
	}
</script>
