import STATE_docs_landing from '@/store/STATE_docs_landing.js';

/**
 * Template_parser
*/
class Template_parser
{
	/**
	 * process_template_object
	*/
	process_template(template_raw)
	{
		let split = template_raw.split('%%');
		let clear_array = [];
		let i, len = split.length;
		for (i = 0; i < len; i++) {
			let occurence = split[i].replace(/\n/g, '').replace(/\t/g, '').trim();
			// send button for tiles inputs
			if (occurence.indexOf('send_button') !== -1) {
				clear_array.push(
					{
						key: 'button_key',
						content: {
							send: occurence.match(/send=\w+/)?.[0].replace('send=', ''),
							text: occurence.match(/text="(.*?)"/)?.[0].replace('text="', '').replace('"', ''),
							group: occurence.match(/group=\w+/)?.[0].replace('group=', ''),
						}
					}
				);
			}
			// html doesnt have ..type=..
			else if (occurence.match(/type=\w+/) === null) {
				if (occurence === '' || occurence.indexOf('display:none') !== -1) {
					continue;
				}
				clear_array.push(
					{
						key: 'html_key',
						content: occurence
					}
				);
			}
			// otherwise its input data
			else {
				clear_array.push(
					// store them in state and return tags for parsed template
					this.create_input_object(occurence)
				);
			}
		}
		// console.log(clear_array);
		return clear_array;
	}


	/**
	 * create_input_object
	*/
	create_input_object(occurence)
	{
		let type = occurence.match(/type=\w+/)?.[0].replace('type=', '');
		// single
		if (type === 'single') {
			let tags = occurence.match(/tags=\w+/)?.[0].replace('tags=', '');
			let send = occurence.match(/send=\w+/)?.[0].replace('send=', '');
			let heading = occurence.match(/heading="(.*?)"/)?.[0].replace('heading="', '').replace('"', '');
			let multiple = parseInt( occurence.match(/multiple=\w+/)?.[0].replace('multiple=', '') , 10);
			let input = {
				type: type,
				group: null,
				tags: tags,
				send: send,
				heading: heading,
				image: null,
				required: false,
				multiple: multiple,
			};
			STATE_docs_landing().add_input_data(tags, input);
			return { key: 'input_key', tag: tags };
		// tiles
		} else if (type === 'tiles') {
			let all_tags = [];
			let group = occurence.match(/group=\w+/)?.[0].replace('group=', '');
			let parts = occurence.split('%');
			let i, len = parts.length;
			for (i = 0; i < len; i++) {
				if (parts[i].match(/type=/)) {
					continue;
				}
				let tags = parts[i].match(/tags=\w+/)?.[0].replace('tags=', '');
				let heading = parts[i].match(/heading="(.*?)"/)?.[0].replace('heading="', '').replace('"', '');
				let image = parts[i].match(/image="(.*?)"/)?.[0].replace('image="', '').replace('"', '');
				let required = parts[i].match(/required=1/) ? true : false;
				let input = {
					type: type,
					group: group,
					tags: tags,
					send: null,
					heading: heading,
					image: image,
					required: required,
					multiple: NaN,
				}
				STATE_docs_landing().add_input_data(tags, input);
				all_tags.push(tags);
			}
			return { key: 'tiles_key', all_tags: all_tags };
		// grouped
		} else if (type === 'grouped') {
			let all_tags = [];
			let tags = occurence.match(/tags=\w+/)?.[0].replace('tags=', '');
			let group = occurence.match(/group=\w+/)?.[0].replace('group=', '');
			let heading = occurence.match(/heading="(.*?)"/)?.[0].replace('heading="', '').replace('"', '');
			let max = parseInt( occurence.match(/max=\w+/)?.[0].replace('max=', '') , 10);
			let i, len = max;
			for (i = 0; i < len; i++) {
				let incremented_tag = tags + (i + 1);
				let input = {
					type: type,
					group: group,
					tags: incremented_tag,
					send: null,
					heading: heading,
					image: null,
					required: false,
					multiple: NaN,
				};
				STATE_docs_landing().add_input_data(incremented_tag, input);
				all_tags.push(incremented_tag);
			}
			STATE_docs_landing().setup_displayed_in_group(group, max, all_tags);
			return { key: 'grouped_key', all_tags: all_tags };
		} else {
			console.log('Parameter type=' + type + ' was not recognized.');
			return {};
		}
	}
}

export default new Template_parser;
